let text = `PREMIRE PARTIE
I
Aujourdhui, maman est morte. Ou peut-tre hier, je ne sais pas. Jai reu un tlgramme de lasile :  Mre dcde. Enterrement demain. Sentiments distingus.  Cela ne veut rien dire. Ctait peut-tre hier.

Lasile de vieillards est  Marengo,  quatre-vingts kilomtres dAlger. Je prendrai lautobus  deux heures et jarriverai dans laprs-midi. Ainsi, je pourrai veiller et je rentrerai demain soir. Jai demand deux jours de cong  mon patron et il ne pouvait pas me les refuser avec une excuse pareille. Mais il navait pas lair content. Je lui ai mme dit :  Ce nest pas de ma faute.  Il na pas rpondu. Jai pens alors que je naurais pas d lui dire cela. En somme, je navais pas  mexcuser. Ctait plutt  lui de me prsenter ses condolances. Mais il le fera sans doute aprs-demain, quand il me verra en deuil. Pour le moment, cest un peu comme si maman ntait pas morte. Aprs lenterrement, au contraire, ce sera une affaire classe et tout aura revtu une allure plus officielle.

Jai pris lautobus  deux heures. Il faisait trs chaud. Jai mang au restaurant, chez Cleste, comme dhabitude. Ils avaient tous beaucoup de peine pour moi et Cleste ma dit :  On na quune mre.  Quand je suis parti, ils mont accompagn  la porte. Jtais un peu tourdi parce quil a fallu que je monte chez Emmanuel pour lui emprunter une cravate noire et un brassard. Il a perdu son oncle, il y a quelques mois.

Jai couru pour ne pas manquer le dpart. Cette hte, cette course, cest  cause de tout cela sans doute, ajout aux cahots,  lodeur dessence,  la rverbration de la route et du ciel, que je me suis assoupi. Jai dormi pendant presque tout le trajet. Et quand je me suis rveill, jtais tass contre un militaire qui ma souri et qui ma demand si je venais de loin. Jai dit  oui  pour navoir plus  parler.

Lasile est  deux kilomtres du village. Jai fait le chemin  pied. Jai voulu voir maman tout de suite. Mais le concierge ma dit quil fallait que je rencontre le directeur. Comme il tait occup, jai attendu un peu. Pendant tout ce temps, le concierge a parl et ensuite, jai vu le directeur : il ma reu dans son bureau. Ctait un petit vieux, avec la Lgion dhonneur. Il ma regard de ses yeux clairs. Puis il ma serr la main quil a garde si longtemps que je ne savais trop comment la retirer. Il a consult un dossier et ma dit :  Mme Meursault est entre ici il y a trois ans. Vous tiez son seul soutien.  Jai cru quil me reprochait quelque chose et jai commenc  lui expliquer. Mais il ma interrompu :  Vous navez pas  vous justifier, mon cher enfant. Jai lu le dossier de votre mre. Vous ne pouviez subvenir  ses besoins. Il lui fallait une garde. Vos salaires sont modestes. Et tout compte fait, elle tait plus heureuse ici.  Jai dit :  Oui, monsieur le Directeur.  Il a ajout :  Vous savez, elle avait des amis, des gens de son ge. Elle pouvait partager avec eux des intrts qui sont dun autre temps. Vous tes jeune et elle devait sennuyer avec vous. 

Ctait vrai. Quand elle tait  la maison, maman passait son temps  me suivre des yeux en silence. Dans les premiers jours o elle tait  lasile, elle pleurait souvent. Mais ctait  cause de lhabitude. Au bout de quelques mois, elle aurait pleur si on lavait retire de lasile. Toujours  cause de lhabitude. Cest un peu pour cela que dans la dernire anne je ny suis presque plus all. Et aussi parce que cela me prenait mon dimanche  sans compter leffort pour aller  lautobus, prendre des tickets et faire deux heures de route.

Le directeur ma encore parl. Mais je ne lcoutais presque plus. Puis il ma dit :  Je suppose que vous voulez voir votre mre.  Je me suis lev sans rien dire et il ma prcd vers la porte. Dans lescalier, il ma expliqu :  Nous lavons transporte dans notre petite morgue. Pour ne pas impressionner les autres. Chaque fois quun pensionnaire meurt, les autres sont nerveux pendant deux ou trois jours. Et a rend le service difficile.  Nous avons travers une cour o il y avait beaucoup de vieillards, bavardant par petits groupes. Ils se taisaient quand nous passions. Et derrire nous, les conversations reprenaient. On aurait dit dun jacassement assourdi de perruches.  la porte dun petit btiment, le directeur ma quitt :  Je vous laisse, monsieur Meursault. Je suis  votre disposition dans mon bureau. En principe, lenterrement est fix  dix heures du matin. Nous avons pens que vous pourrez ainsi veiller la disparue. Un dernier mot : votre mre a, parat-il, exprim souvent  ses compagnons le dsir dtre enterre religieusement. Jai pris sur moi de faire le ncessaire. Mais je voulais vous en informer.  Je lai remerci. Maman, sans tre athe, navait jamais pens de son vivant  la religion.

Je suis entr. Ctait une salle trs claire, blanchie  la chaux et recouverte dune verrire. Elle tait meuble de chaises et de chevalets en forme de X. Deux dentre eux, au centre, supportaient une bire recouverte de son couvercle. On voyait seulement des vis brillantes,  peine enfonces, se dtacher sur les planches passes au brou de noix. Prs de la bire, il y avait une infirmire arabe en sarrau blanc, un foulard de couleur vive sur la tte.

 ce moment, le concierge est entr derrire mon dos. Il avait d courir. Il a bgay un peu :  On la couverte, mais je dois dvisser la bire pour que vous puissiez la voir.  Il sapprochait de la bire quand je lai arrt. Il ma dit :  Vous ne voulez pas ?  Jai rpondu :  Non.  Il sest interrompu et jtais gn parce que je sentais que je naurais pas d dire cela. Au bout dun moment, il ma regard et il ma demand :  Pourquoi ?  mais sans reproche, comme sil sinformait. Jai dit :  Je ne sais pas.  Alors, tortillant sa moustache blanche, il a dclar sans me regarder :  Je comprends.  Il avait de beaux yeux, bleu clair, et un teint un peu rouge. Il ma donn une chaise et lui-mme sest assis un peu en arrire de moi. La garde sest leve et sest dirige vers la sortie.  ce moment, le concierge ma dit :  Cest un chancre quelle a.  Comme je ne comprenais pas, jai regard linfirmire et jai vu quelle portait sous les yeux un bandeau qui faisait le tour de la tte.  la hauteur du nez, le bandeau tait plat. On ne voyait que la blancheur du bandeau dans son visage.

Quand elle est partie, le concierge a parl :  Je vais vous laisser seul.  Je ne sais pas quel geste jai fait, mais il est rest, debout derrire moi. Cette prsence dans mon dos me gnait. La pice tait pleine dune belle lumire de fin daprs-midi. Deux frelons bourdonnaient contre la verrire. Et je sentais le sommeil me gagner. Jai dit au concierge, sans me retourner vers lui :  Il y a longtemps que vous tes l ?  Immdiatement il a rpondu :  Cinq ans   comme sil avait attendu depuis toujours ma demande.

Ensuite, il a beaucoup bavard. On laurait bien tonn en lui disant quil finirait concierge  lasile de Marengo. Il avait soixante-quatre ans et il tait Parisien.  ce moment je lai interrompu :  Ah ! vous ntes pas dici ?  Puis je me suis souvenu quavant de me conduire chez le directeur, il mavait parl de maman. Il mavait dit quil fallait lenterrer trs vite, parce que dans la plaine il faisait chaud, surtout dans ce pays. Cest alors quil mavait appris quil avait vcu  Paris et quil avait du mal  loublier.  Paris, on reste avec le mort trois, quatre jours quelquefois. Ici on na pas le temps, on ne sest pas fait  lide que dj il faut courir derrire le corbillard. Sa femme lui avait dit alors :  Tais-toi, ce ne sont pas des choses  raconter  monsieur.  Le vieux avait rougi et stait excus. Jtais intervenu pour dire :  Mais non. Mais non.  Je trouvais ce quil racontait juste et intressant.

Dans la petite morgue, il ma appris quil tait entr  lasile comme indigent. Comme il se sentait valide, il stait propos pour cette place de concierge. Je lui ai fait remarquer quen somme il tait un pensionnaire. Il ma dit que non. Javais dj t frapp par la faon quil avait de dire :  ils ,  les autres , et plus rarement  les vieux , en parlant des pensionnaires dont certains ntaient pas plus gs que lui. Mais naturellement, ce ntait pas la mme chose. Lui tait concierge, et, dans une certaine mesure, il avait des droits sur eux.

La garde est entre  ce moment. Le soir tait tomb brusquement. Trs vite, la nuit stait paissie au-dessus de la verrire. Le concierge a tourn le commutateur et jai t aveugl par lclaboussement soudain de la lumire. Il ma invit  me rendre au rfectoire pour dner. Mais je navais pas faim. Il ma offert alors dapporter une tasse de caf au lait. Comme jaime beaucoup le caf au lait, jai accept et il est revenu un moment aprs avec un plateau. Jai bu. Jai eu alors envie de fumer. Mais jai hsit parce que je ne savais pas si je pouvais le faire devant maman. Jai rflchi, cela navait aucune importance. Jai offert une cigarette au concierge et nous avons fum.

 un moment, il ma dit :  Vous savez, les amis de madame votre mre vont venir la veiller aussi. Cest la coutume. Il faut que jaille chercher des chaises et du caf noir.  Je lui ai demand si on pouvait teindre une des lampes. Lclat de la lumire sur les murs blancs me fatiguait. Il ma dit que ce ntait pas possible. Linstallation tait ainsi faite : ctait tout ou rien. Je nai plus beaucoup fait attention  lui. Il est sorti, est revenu, a dispos des chaises. Sur lune delles, il a empil des tasses autour dune cafetire. Puis il sest assis en face de moi, de lautre ct de maman. La garde tait aussi au fond, le dos tourn. Je ne voyais pas ce quelle faisait. Mais au mouvement de ses bras, je pouvais croire quelle tricotait. Il faisait doux, le caf mavait rchauff et par la porte ouverte entrait une odeur de nuit et de fleurs. Je crois que jai somnol un peu.

Cest un frlement qui ma rveill. Davoir ferm les yeux, la pice ma paru encore plus clatante de blancheur. Devant moi, il ny avait pas une ombre et chaque objet, chaque angle, toutes les courbes se dessinaient avec une puret blessante pour les yeux. Cest  ce moment que les amis de maman sont entrs. Ils taient en tout une dizaine, et ils glissaient en silence dans cette lumire aveuglante. Ils se sont assis sans quaucune chaise grint. Je les voyais comme je nai jamais vu personne et pas un dtail de leurs visages ou de leurs habits ne mchappait. Pourtant je ne les entendais pas et javais peine  croire  leur ralit. Presque toutes les femmes portaient un tablier et le cordon qui les serrait  la taille faisait encore ressortir leur ventre bomb. Je navais encore jamais remarqu  quel point les vieilles femmes pouvaient avoir du ventre. Les hommes taient presque tous trs maigres et tenaient des cannes. Ce qui me frappait dans leurs visages, cest que je ne voyais pas leurs yeux, mais seulement une lueur sans clat au milieu dun nid de rides. Lorsquils se sont assis, la plupart mont regard et ont hoch la tte avec gne, les lvres toutes manges par leur bouche sans dents, sans que je puisse savoir sils me saluaient ou sil sagissait dun tic. Je crois plutt quils me saluaient. Cest  ce moment que je me suis aperu quils taient tous assis en face de moi  dodeliner de la tte, autour du concierge. Jai eu un moment limpression ridicule quils taient l pour me juger.

Peu aprs, une des femmes sest mise  pleurer. Elle tait au second rang, cache par une de ses compagnes, et je la voyais mal. Elle pleurait  petits cris, rgulirement : il me semblait quelle ne sarrterait jamais. Les autres avaient lair de ne pas lentendre. Ils taient affaisss, mornes et silencieux. Ils regardaient la bire ou leur canne, ou nimporte quoi, mais ils ne regardaient que cela. La femme pleurait toujours. Jtais trs tonn parce que je ne la connaissais pas. Jaurais voulu ne plus lentendre. Pourtant je nosais pas le lui dire. Le concierge sest pench vers elle, lui a parl, mais elle a secou la tte, a bredouill quelque chose, et a continu de pleurer avec la mme rgularit. Le concierge est venu alors de mon ct. Il sest assis prs de moi. Aprs un assez long moment, il ma renseign sans me regarder :  Elle tait trs lie avec madame votre mre. Elle dit que ctait sa seule amie ici et que maintenant elle na plus personne. 

Nous sommes rests un long moment ainsi. Les soupirs et les sanglots de la femme se faisaient plus rares. Elle reniflait beaucoup. Elle sest tue enfin. Je navais plus sommeil, mais jtais fatigu et les reins me faisaient mal.  prsent ctait le silence de tous ces gens qui mtait pnible. De temps en temps seulement, jentendais un bruit singulier et je ne pouvais comprendre ce quil tait.  la longue, jai fini par deviner que quelques-uns dentre les vieillards suaient lintrieur de leurs joues et laissaient chapper ces clappements bizarres. Ils ne sen apercevaient pas tant ils taient absorbs dans leurs penses. Javais mme limpression que cette morte, couche au milieu deux, ne signifiait rien  leurs yeux. Mais je crois maintenant que ctait une impression fausse.

Nous avons tous pris du caf, servi par le concierge. Ensuite, je ne sais plus. La nuit a pass. Je me souviens qu un moment jai ouvert les yeux et jai vu que les vieillards dormaient tasss sur eux-mmes,  lexception dun seul qui, le menton sur le dos de ses mains agrippes  la canne, me regardait fixement comme sil nattendait que mon rveil. Puis jai encore dormi. Je me suis rveill parce que javais de plus en plus mal aux reins. Le jour glissait sur la verrire. Peu aprs, lun des vieillards sest rveill et il a beaucoup touss. Il crachait dans un grand mouchoir  carreaux et chacun de ses crachats tait comme un arrachement. Il a rveill les autres et le concierge a dit quils devraient partir. Ils se sont levs. Cette veille incommode leur avait fait des visages de cendre. En sortant, et  mon grand tonnement, ils mont tous serr la main  comme si cette nuit o nous navions pas chang un mot avait accru notre intimit.

Jtais fatigu. Le concierge ma conduit chez lui et jai pu faire un peu de toilette. Jai encore pris du caf au lait qui tait trs bon. Quand je suis sorti, le jour tait compltement lev. Au-dessus des collines qui sparent Marengo de la mer, le ciel tait plein de rougeurs. Et le vent qui passait au-dessus delles apportait ici une odeur de sel. Ctait une belle journe qui se prparait. Il y avait longtemps que jtais all  la campagne et je sentais quel plaisir jaurais pris  me promener sil ny avait pas eu maman.

Mais jai attendu dans la cour, sous un platane. Je respirais lodeur de la terre frache et je navais plus sommeil. Jai pens aux collgues du bureau.  cette heure, ils se levaient pour aller au travail : pour moi ctait toujours lheure la plus difficile. Jai encore rflchi un peu  ces choses, mais jai t distrait par une cloche qui sonnait  lintrieur des btiments. Il y a eu du remue-mnage derrire les fentres, puis tout sest calm. Le soleil tait mont un peu plus dans le ciel : il commenait  chauffer mes pieds. Le concierge a travers la cour et ma dit que le directeur me demandait. Je suis all dans son bureau. Il ma fait signer un certain nombre de pices. Jai vu quil tait habill de noir avec un pantalon ray. Il a pris le tlphone en main et il ma interpell :  Les employs des pompes funbres sont l depuis un moment. Je vais leur demander de venir fermer la bire. Voulez-vous auparavant voir votre mre une dernire fois ?  Jai dit non. Il a ordonn dans le tlphone en baissant la voix :  Figeac, dites aux hommes quils peuvent aller. 

Ensuite il ma dit quil assisterait  lenterrement et je lai remerci. Il sest assis derrire son bureau, il a crois ses petites jambes. Il ma averti que moi et lui serions seuls, avec linfirmire de service. En principe, les pensionnaires ne devaient pas assister aux enterrements. Il les laissait seulement veiller :  Cest une question dhumanit , a-t-il remarqu. Mais en lespce, il avait accord lautorisation de suivre le convoi  un vieil ami de maman :  Thomas Prez.  Ici, le directeur a souri. Il ma dit :  Vous comprenez, cest un sentiment un peu puril. Mais lui et votre mre ne se quittaient gure.  lasile, on les plaisantait, on disait  Prez :  Cest votre fiance.  Lui riait. a leur faisait plaisir. Et le fait est que la mort de Mme Meursault la beaucoup affect. Je nai pas cru devoir lui refuser lautorisation. Mais sur le conseil du mdecin visiteur, je lui ai interdit la veille dhier. 

Nous sommes rests silencieux assez longtemps. Le directeur sest lev et a regard par la fentre de son bureau.  un moment, il a observ :  Voil dj le cur de Marengo. Il est en avance.  Il ma prvenu quil faudrait au moins trois quarts dheure de marche pour aller  lglise qui est au village mme. Nous sommes descendus. Devant le btiment, il y avait le cur et deux enfants de chur. Lun de ceux-ci tenait un encensoir et le prtre se baissait vers lui pour rgler la longueur de la chane dargent. Quand nous sommes arrivs, le prtre sest relev. Il ma appel  mon fils  et ma dit quelques mots. Il est entr ; je lai suivi.

Jai vu dun coup que les vis de la bire taient enfonces et quil y avait quatre hommes noirs dans la pice. Jai entendu en mme temps le directeur me dire que la voiture attendait sur la route et le prtre commencer ses prires.  partir de ce moment, tout est all trs vite. Les hommes se sont avancs vers la bire avec un drap. Le prtre, ses suivants, le directeur et moi-mme sommes sortis. Devant la porte, il y avait une dame que je ne connaissais pas :  M. Meursault , a dit le directeur. Je nai pas entendu le nom de cette dame et jai compris seulement quelle tait infirmire dlgue. Elle a inclin sans un sourire son visage osseux et long. Puis nous nous sommes rangs pour laisser passer le corps. Nous avons suivi les porteurs et nous sommes sortis de lasile. Devant la porte, il y avait la voiture. Vernie, oblongue et brillante, elle faisait penser  un plumier.  ct delle, il y avait, lordonnateur, petit homme aux habits ridicules, et un vieillard  lallure emprunte. Jai compris que ctait M. Prez. Il avait un feutre mou  la calotte ronde et aux ailes larges (il la t quand la bire a pass la porte), un costume dont le pantalon tirebouchonnait sur les souliers et un nud dtoffe noire trop petit pour sa chemise  grand col blanc. Ses lvres tremblaient au-dessous dun nez truff de points noirs. Ses cheveux blancs assez fins laissaient passer de curieuses oreilles ballantes et mal ourles dont la couleur rouge sang dans ce visage blafard me frappa. Lordonnateur nous donna nos places. Le cur marchait en avant, puis la voiture. Autour delle, les quatre hommes. Derrire, le directeur, moi-mme et, fermant la marche, linfirmire dlgue et M. Prez.

Le ciel tait dj plein de soleil. Il commenait  peser sur la terre et la chaleur augmentait rapidement. Je ne sais pas pourquoi nous avons attendu assez longtemps avant de nous mettre en marche. Javais chaud sous mes vtements sombres. Le petit vieux, qui stait recouvert, a de nouveau t son chapeau. Je mtais un peu tourn de son ct, et je le regardais lorsque le directeur ma parl de lui. Il ma dit que souvent ma mre et M. Prez allaient se promener le soir jusquau village, accompagns dune infirmire. Je regardais la campagne autour de moi.  travers les lignes de cyprs qui menaient aux collines prs du ciel, cette terre rousse et verte, ces maisons rares et bien dessines, je comprenais maman. Le soir, dans ce pays, devait tre comme une trve mlancolique. Aujourdhui, le soleil dbordant qui faisait tressaillir le paysage le rendait inhumain et dprimant.

Nous nous sommes mis en marche. Cest  ce moment que je me suis aperu que Prez claudiquait lgrement. La voiture, peu  peu, prenait de la vitesse et le vieillard perdait du terrain. Lun des hommes qui entouraient la voiture stait laiss dpasser aussi et marchait maintenant  mon niveau. Jtais surpris de la rapidit avec laquelle le soleil montait dans le ciel. Je me suis aperu quil y avait dj longtemps que la campagne bourdonnait du chant des insectes et de crpitements dherbe. La sueur coulait sur mes joues. Comme je navais pas de chapeau, je mventais avec mon mouchoir. Lemploy des pompes funbres ma dit alors quelque chose que je nai pas entendu. En mme temps, il sessuyait le crne avec un mouchoir quil tenait dans sa main gauche, la main droite soulevant le bord de sa casquette. Je lui ai dit :  Comment ?  Il a rpt en montrant le ciel :  a tape.  Jai dit :  Oui.  Un peu aprs, il ma demand :  Cest votre mre qui est l ?  Jai encore dit :  Oui.   Elle tait vieille ?  Jai rpondu :  Comme a , parce que je ne savais pas le chiffre exact. Ensuite, il sest tu. Je me suis retourn et jai vu le vieux Prez  une cinquantaine de mtres derrire nous. Il se htait en balanant son feutre  bout de bras. Jai regard aussi le directeur. Il marchait avec beaucoup de dignit, sans un geste inutile. Quelques gouttes de sueur perlaient sur son front, mais il ne les essuyait pas.

Il me semblait que le convoi marchait un peu plus vite. Autour de moi, ctait toujours la mme campagne lumineuse gorge de soleil. Lclat du ciel tait insoutenable.  un moment donn, nous sommes passs sur une partie de la route qui avait t rcemment refaite. Le soleil avait fait clater le goudron. Les pieds y enfonaient et laissaient ouverte sa pulpe brillante. Au-dessus de la voiture, le chapeau du cocher, en cuir bouilli, semblait avoir t ptri dans cette boue noire. Jtais un peu perdu entre le ciel bleu et blanc et la monotonie de ces couleurs, noir gluant du goudron ouvert, noir terne des habits, noir laqu de la voiture. Tout cela, le soleil, lodeur de cuir et de crottin de la voiture, celle du vernis et celle de lencens, la fatigue dune nuit dinsomnie, me troublait le regard et les ides. Je me suis retourn une fois de plus : Prez ma paru trs loin, perdu dans une nue de chaleur, puis je ne lai plus aperu. Je lai cherch du regard et jai vu quil avait quitt la route et pris  travers champs. Jai constat aussi que devant moi la route tournait. Jai compris que Prez qui connaissait le pays coupait au plus court pour nous rattraper. Au tournant il nous avait rejoints. Puis nous lavons perdu. Il a repris encore  travers champs et comme cela plusieurs fois. Moi, je sentais le sang qui me battait aux tempes.

Tout sest pass ensuite avec tant de prcipitation, de certitude et de naturel, que je ne me souviens plus de rien. Une chose seulement :  lentre du village, linfirmire dlgue ma parl. Elle avait une voix singulire qui nallait pas avec son visage, une voix mlodieuse et tremblante. Elle ma dit :  Si on va doucement, on risque une insolation. Mais si on va trop vite, on est en transpiration et dans lglise on attrape un chaud et froid.  Elle avait raison. Il ny avait pas dissue. Jai encore gard quelques images de cette journe : par exemple, le visage de Prez quand, pour la dernire fois, il nous a rejoints prs du village. De grosses larmes dnervement et de peine ruisselaient sur ses joues. Mais,  cause des rides, elles ne scoulaient pas. Elles stalaient, se rejoignaient et formaient un vernis deau sur ce visage dtruit. Il y a eu encore lglise et les villageois sur les trottoirs, les graniums rouges sur les tombes du cimetire, lvanouissement de Prez (on et dit un pantin disloqu), la terre couleur de sang qui roulait sur la bire de maman, la chair blanche des racines qui sy mlaient, encore du monde, des voix, le village, lattente devant un caf, lincessant ronflement du moteur, et ma joie quand lautobus est entr dans le nid de lumires dAlger et que jai pens que jallais me coucher et dormir pendant douze heures.

II
En me rveillant, jai compris pourquoi mon patron avait lair mcontent quand je lui ai demand mes deux jours de cong : cest aujourdhui samedi. Je lavais pour ainsi dire oubli, mais en me levant, cette ide mest venue. Mon patron, tout naturellement, a pens que jaurais ainsi quatre jours de vacances avec mon dimanche et cela ne pouvait pas lui faire plaisir. Mais dune part, ce nest pas de ma faute si on a enterr maman hier au lieu daujourdhui et dautre part, jaurais eu mon samedi et mon dimanche de toute faon. Bien entendu, cela ne mempche pas de comprendre tout de mme mon patron.

Jai eu de la peine  me lever parce que jtais fatigu de ma journe dhier. Pendant que je me rasais, je me suis demand ce que jallais faire et jai dcid daller me baigner. Jai pris le tram pour aller  ltablissement de bains du port. L, jai plong dans la passe. Il y avait beaucoup de jeunes gens. Jai retrouv dans leau Marie Cardona, une ancienne dactylo de mon bureau dont javais eu envie  lpoque. Elle aussi, je crois. Mais elle est partie peu aprs et nous navons pas eu le temps. Je lai aide  monter sur une boue et, dans ce mouvement, jai effleur ses seins. Jtais encore dans leau quand elle tait dj  plat ventre sur la boue. Elle sest retourne vers moi. Elle avait les cheveux dans les yeux et elle riait. Je me suis hiss  ct delle sur la boue. Il faisait bon et, comme en plaisantant, jai laiss aller ma tte en arrire et je lai pose sur son ventre. Elle na rien dit et je suis rest ainsi. Javais tout le ciel dans les yeux et il tait bleu et dor. Sous ma nuque, je sentais le ventre de Marie battre doucement. Nous sommes rests longtemps sur la boue,  moiti endormis. Quand le soleil est devenu trop fort, elle a plong et je lai suivie. Je lai rattrape, jai pass ma main autour de sa taille et nous avons nag ensemble. Elle riait toujours. Sur le quai, pendant que nous nous schions, elle ma dit :  Je suis plus brune que vous.  Je lui ai demand si elle voulait venir au cinma, le soir. Elle a encore ri et ma dit quelle avait envie de voir un film avec Fernandel. Quand nous nous sommes rhabills, elle a eu lair trs surprise de me voir avec une cravate noire et elle ma demand si jtais en deuil. Je lui ai dit que maman tait morte. Comme elle voulait savoir depuis quand, jai rpondu :  Depuis hier.  Elle a eu un petit recul, mais na fait aucune remarque. Jai eu envie de lui dire que ce ntait pas de ma faute, mais je me suis arrt parce que jai pens que je lavais dj dit  mon patron. Cela ne signifiait rien. De toute faon, on est toujours un peu fautif.

Le soir, Marie avait tout oubli. Le film tait drle par moments et puis vraiment trop bte. Elle avait sa jambe contre la mienne. Je lui caressais les seins. Vers la fin de la sance, je lai embrasse, mais mal. En sortant, elle est venue chez moi.

Quand je me suis rveill, Marie tait partie. Elle mavait expliqu quelle devait aller chez sa tante. Jai pens que ctait dimanche et cela ma ennuy : je naime pas le dimanche. Alors, je me suis retourn dans mon lit, jai cherch dans le traversin lodeur de sel que les cheveux de Marie y avaient laisse et jai dormi jusqu dix heures. Jai fum ensuite des cigarettes, toujours couch, jusqu midi. Je ne voulais pas djeuner chez Cleste comme dhabitude parce que, certainement, ils mauraient pos des questions et je naime pas cela. Je me suis fait cuire des ufs et je les ai mangs  mme le plat, sans pain parce que je nen avais plus et que je ne voulais pas descendre pour en acheter.

Aprs le djeuner, je me suis ennuy un peu et jai err dans lappartement. Il tait commode quand maman tait l. Maintenant il est trop grand pour moi et jai d transporter dans ma chambre la table de la salle  manger. Je ne vis plus que dans cette pice, entre les chaises de paille un peu creuses, larmoire dont la glace est jaunie, la table de toilette et le lit de cuivre. Le reste est  labandon. Un peu plus tard, pour faire quelque chose, jai pris un vieux journal et je lai lu. Jy ai dcoup une rclame des sels Kruschen et je lai colle dans un vieux cahier o je mets les choses qui mamusent dans les journaux. Je me suis aussi lav les mains et, pour finir, je me suis mis au balcon.

Ma chambre donne sur la rue principale du faubourg. Laprs-midi tait beau. Cependant, le pav tait gras, les gens rares et presss encore. Ctaient dabord des familles allant en promenade, deux petits garons en costume marin, la culotte au-dessous du genou, un peu emptrs dans leurs vtements raides, et une petite fille avec un gros nud rose et des souliers noirs vernis. Derrire eux, une mre norme, en robe de soie marron, et le pre, un petit homme assez frle que je connais de vue. Il avait un canotier, un nud papillon et une canne  la main. En le voyant avec sa femme, jai compris pourquoi dans le quartier on disait de lui quil tait distingu. Un peu plus tard passrent les jeunes gens du faubourg, cheveux laqus et cravate rouge, le veston trs cintr, avec une pochette brode et des souliers  bouts carrs. Jai pens quils allaient aux cinmas du centre. Ctait pourquoi ils partaient si tt et se dpchaient vers le tram en riant trs fort.

Aprs eux, la rue peu  peu est devenue dserte. Les spectacles taient partout commencs, je crois. Il ny avait plus dans la rue que les boutiquiers et les chats. Le ciel tait pur mais sans clat au-dessus des ficus qui bordent la rue. Sur le trottoir den face, le marchand de tabac a sorti une chaise, la installe devant sa porte et la enfourche en sappuyant des deux bras sur le dossier. Les trams tout  lheure bonds taient presque vides. Dans le petit caf  Chez Pierrot ,  ct du marchand de tabac, le garon balayait de la sciure dans la salle dserte. Ctait vraiment dimanche.

Jai retourn ma chaise et je lai place comme celle du marchand de tabac parce que jai trouv que ctait plus commode. Jai fum deux cigarettes, je suis rentr pour prendre un morceau de chocolat et je suis revenu le manger  la fentre. Peu aprs, le ciel sest assombri et jai cru que nous allions avoir un orage dt. Il sest dcouvert peu  peu cependant. Mais le passage des nues avait laiss sur la rue comme une promesse de pluie qui la rendue plus sombre. Je suis rest longtemps  regarder le ciel.

 cinq heures, des tramways sont arrivs dans le bruit. Ils ramenaient du stade de banlieue des grappes de spectateurs perchs sur les marchepieds et les rambardes. Les tramways suivants ont ramen les joueurs que jai reconnus  leurs petites valises. Ils hurlaient et chantaient  pleins poumons que leur club ne prirait pas. Plusieurs mont fait des signes. Lun ma mme cri :  On les a eus.  Et jai fait :  Oui , en secouant la tte.  partir de ce moment, les autos ont commenc  affluer.

La journe a tourn encore un peu. Au-dessus des toits, le ciel est devenu rougetre et, avec le soir naissant, les rues se sont animes. Les promeneurs revenaient peu  peu. Jai reconnu le monsieur distingu au milieu dautres. Les enfants pleuraient ou se laissaient traner. Presque aussitt, les cinmas du quartier ont dvers dans la rue un flot de spectateurs. Parmi eux, les jeunes gens avaient des gestes plus dcids que dhabitude et jai pens quils avaient vu un film daventures. Ceux qui revenaient des cinmas de la ville arrivrent un peu plus tard. Ils semblaient plus graves. Ils riaient encore, mais de temps en temps, ils paraissaient fatigus et songeurs. Ils sont rests dans la rue, allant et venant sur le trottoir den face. Les jeunes filles du quartier, en cheveux, se tenaient par le bras. Les jeunes gens staient arrangs pour les croiser et ils lanaient des plaisanteries dont elles riaient en dtournant la tte. Plusieurs dentre elles, que je connaissais, mont fait des signes.

Les lampes de la rue se sont alors allumes brusquement et elles ont fait plir les premires toiles qui montaient dans la nuit. Jai senti mes yeux se fatiguer  regarder les trottoirs avec leur chargement dhommes et de lumires. Les lampes faisaient luire le pav mouill, et les tramways,  intervalles rguliers, mettaient leurs reflets sur des cheveux brillants, un sourire ou un bracelet dargent. Peu aprs, avec les tramways plus rares et la nuit dj noire au-dessus des arbres et des lampes, le quartier sest vid insensiblement, jusqu ce que le premier chat traverse lentement la rue de nouveau dserte. Jai pens alors quil fallait dner. Javais un peu mal au cou dtre rest longtemps appuy sur le dos de ma chaise. Je suis descendu acheter du pain et des ptes, jai fait ma cuisine et jai mang debout. Jai voulu fumer une cigarette  la fentre, mais lair avait frachi et jai eu un peu froid. Jai ferm mes fentres et en revenant jai vu dans la glace un bout de table o ma lampe  alcool voisinait avec des morceaux de pain. Jai pens que ctait toujours un dimanche de tir, que maman tait maintenant enterre, que jallais reprendre mon travail et que, somme toute, il ny avait rien de chang.

III
Aujourdhui jai beaucoup travaill au bureau. Le patron a t aimable. Il ma demand si je ntais pas trop fatigu et il a voulu savoir aussi lge de maman. Jai dit  une soixantaine dannes , pour ne pas me tromper et je ne sais pas pourquoi il a eu lair dtre soulag et de considrer que ctait une affaire termine.

Il y avait un tas de connaissements qui samoncelaient sur ma table et il a fallu que je les dpouille tous. Avant de quitter le bureau pour aller djeuner, je me suis lav les mains.  midi, jaime bien ce moment. Le soir, jy trouve moins de plaisir parce que la serviette roulante quon utilise est tout  fait humide : elle a servi toute la journe. Jen ai fait la remarque un jour  mon patron. Il ma rpondu quil trouvait cela regrettable, mais que ctait tout de mme un dtail sans importance. Je suis sorti un peu tard,  midi et demi, avec Emmanuel, qui travaille  lexpdition. Le bureau donne sur la mer et nous avons perdu un moment  regarder les cargos dans le port brlant de soleil.  ce moment, un camion est arriv dans un fracas de chanes et dexplosions. Emmanuel ma demand  si on y allait  et je me suis mis  courir. Le camion nous a dpasss et nous nous sommes lancs  sa poursuite. Jtais noy dans le bruit et la poussire. Je ne voyais plus rien et ne sentais que cet lan dsordonn de la course, au milieu des treuils et des machines, des mts qui dansaient sur lhorizon et des coques que nous longions. Jai pris appui le premier et jai saut au vol. Puis jai aid Emmanuel  sasseoir. Nous tions hors de souffle, le camion sautait sur les pavs ingaux du quai, au milieu de la poussire et du soleil. Emmanuel riait  perdre haleine.

Nous sommes arrivs en nage chez Cleste. Il tait toujours l, avec son gros ventre, son tablier et ses moustaches blanches. Il ma demand si  a allait quand mme . Je lui ai dit que oui et que javais faim. Jai mang trs vite et jai pris du caf. Puis je suis rentr chez moi, jai dormi un peu parce que javais trop bu de vin et, en me rveillant, jai eu envie de fumer. Il tait tard et jai couru pour attraper un tram. Jai travaill tout laprs-midi. Il faisait trs chaud dans le bureau et le soir, en sortant, jai t heureux de revenir en marchant lentement le long des quais. Le ciel tait vert, je me sentais content. Tout de mme, je suis rentr directement chez moi parce que je voulais me prparer des pommes de terre bouillies.

En montant, dans lescalier noir, jai heurt le vieux Salamano, mon voisin de palier. Il tait avec son chien. Il y a huit ans quon les voit ensemble. Lpagneul a une maladie de peau, le rouge, je crois, qui lui fait perdre presque tous ses poils et qui le couvre de plaques et de crotes brunes.  force de vivre avec lui, seuls tous les deux dans une petite chambre, le vieux Salamano a fini par lui ressembler. Il a des crotes rougetres sur le visage et le poil jaune et rare. Le chien, lui, a pris de son patron une sorte dallure vote, le museau en avant et le cou tendu. Ils ont lair de la mme race et pourtant ils se dtestent. Deux fois par jour,  onze heures et  six heures, le vieux mne son chien promener. Depuis huit ans, ils nont pas chang leur itinraire. On peut les voir le long de la rue de Lyon, le chien tirant lhomme jusqu ce que le vieux Salamano bute. Il bat son chien alors et il linsulte. Le chien rampe de frayeur et se laisse traner.  ce moment, cest au vieux de le tirer. Quand le chien a oubli, il entrane de nouveau son matre et il est de nouveau battu et insult. Alors, ils restent tous les deux sur le trottoir et ils se regardent, le chien avec terreur, lhomme avec haine. Cest ainsi tous les jours. Quand le chien veut uriner, le vieux ne lui en laisse pas le temps et il le tire, lpagneul semant derrire lui une trane de petites gouttes. Si par hasard le chien fait dans la chambre, alors il est encore battu. Il y a huit ans que cela dure. Cleste dit toujours que  cest malheureux , mais au fond, personne ne peut savoir. Quand je lai rencontr dans lescalier, Salamano tait en train dinsulter son chien. Il lui disait :  Salaud ! Charogne !  et le chien gmissait. Jai dit :  Bonsoir , mais le vieux insultait toujours. Alors je lui ai demand ce que le chien lui avait fait. Il ne ma pas rpondu. Il disait seulement :  Salaud ! Charogne !  Je le devinais, pench sur son chien, en train darranger quelque chose sur le collier. Jai parl plus fort. Alors sans se retourner, il ma rpondu avec une sorte de rage rentre :  Il est toujours l.  Puis il est parti en tirant la bte qui se laissait traner sur ses quatre pattes, et gmissait.

Juste  ce moment est entr mon deuxime voisin de palier. Dans le quartier, on dit quil vit des femmes. Quand on lui demande son mtier, pourtant, il est  magasinier . En gnral, il nest gure aim. Mais il me parle souvent et quelquefois il passe un moment chez moi parce que je lcoute. Je trouve que ce quil dit est intressant. Dailleurs, je nai aucune raison de ne pas lui parler. Il sappelle Raymond Sints. Il est assez petit, avec de larges paules et un nez de boxeur. Il est toujours habill trs correctement. Lui aussi ma dit, en parlant de Salamano :  Si cest pas malheureux !  Il ma demand si a ne me dgotait pas et jai rpondu que non.

Nous sommes monts et jallais le quitter quand il ma dit :  Jai chez moi du boudin et du vin. Si vous voulez manger un morceau avec moi ?  Jai pens que cela mviterait de faire ma cuisine et jai accept. Lui aussi na quune chambre, avec une cuisine sans fentre. Au-dessus de son lit, il a un ange en stuc blanc et rose, des photos de champions et deux ou trois clichs de femmes nues. La chambre tait sale et le lit dfait. Il a dabord allum sa lampe  ptrole, puis il a sorti un pansement assez douteux de sa poche et a envelopp sa main droite. Je lui ai demand ce quil avait. Il ma dit quil avait eu une bagarre avec un type qui lui cherchait des histoires.

 Vous comprenez, monsieur Meursault, ma-t-il dit, cest pas que je suis mchant, mais je suis vif. Lautre, il ma dit :  Descends du tram si tu es un homme.  Je lui ai dit :  Allez, reste tranquille.  Il ma dit que je ntais pas un homme. Alors je suis descendu et je lui ai dit :  Assez, a vaut mieux, ou je vais te mrir.  Il ma rpondu :  De quoi ?  Alors je lui en ai donn un. Il est tomb. Moi, jallais le relever. Mais il ma donn des coups de pied de par terre. Alors je lui ai donn un coup de genou et deux taquets. Il avait la figure en sang. Je lui ai demand sil avait son compte. Il ma dit :  Oui. 

Pendant tout ce temps, Sints arrangeait son pansement. Jtais assis sur le lit. Il ma dit :  Vous voyez que je ne lai pas cherch. Cest lui qui ma manqu.  Ctait vrai et je lai reconnu. Alors il ma dclar que, justement, il voulait me demander un conseil au sujet de cette affaire, que moi, jtais un homme, je connaissais la vie, que je pouvais laider et quensuite il serait mon copain. Je nai rien dit et il ma demand encore si je voulais tre son copain. Jai dit que a mtait gal : il a eu lair content. Il a sorti du boudin, il la fait cuire  la pole, et il a install des verres, des assiettes, des couverts et deux bouteilles de vin. Tout cela en silence. Puis nous nous sommes installs. En mangeant, il a commenc  me raconter son histoire. Il hsitait dabord un peu.  Jai connu une dame ctait pour autant dire ma matresse.  Lhomme avec qui il stait battu tait le frre de cette femme. Il ma dit quil lavait entretenue. Je nai rien rpondu et pourtant il a ajout tout de suite quil savait ce quon disait dans le quartier, mais quil avait sa conscience pour lui et quil tait magasinier.

 Pour en venir  mon histoire, ma-t-il dit, je me suis aperu quil y avait de la tromperie.  Il lui donnait juste de quoi vivre. Il payait lui-mme le loyer de sa chambre et il lui donnait vingt francs par jour pour la nourriture.  Trois cents francs de chambre, six cents francs de nourriture, une paire de bas de temps en temps, a faisait mille francs. Et madame ne travaillait pas. Mais elle me disait que ctait juste, quelle narrivait pas avec ce que je lui donnais. Pourtant, je lui disais :  Pourquoi tu travailles pas une demi-journe ? Tu me soulagerais bien pour toutes ces petites choses. Je tai achet un ensemble ce mois-ci, je te paye vingt francs par jour, je te paye le loyer et toi, tu prends le caf laprs-midi avec tes amies. Tu leur donnes le caf et le sucre. Moi, je te donne largent. Jai bien agi avec toi et tu me le rends mal.  Mais elle ne travaillait pas, elle disait toujours quelle narrivait pas et cest comme a que je me suis aperu quil y avait de la tromperie. 

Il ma alors racont quil avait trouv un billet de loterie dans son sac et quelle navait pas pu lui expliquer comment elle lavait achet. Un peu plus tard, il avait trouv chez elle  une indication  du mont-de-pit qui prouvait quelle avait engag deux bracelets. Jusque-l, il ignorait lexistence de ces bracelets.  Jai bien vu quil y avait de la tromperie. Alors, je lai quitte. Mais dabord, je lai tape. Et puis, je lui ai dit ses vrits. Je lui ai dit que tout ce quelle voulait, ctait samuser avec sa chose. Comme je lui ai dit, vous comprenez, monsieur Meursault :  Tu ne vois pas que le monde il est jaloux du bonheur que je te donne. Tu connatras plus tard le bonheur que tu avais. 

Il lavait battue jusquau sang. Auparavant, il ne la battait pas.  Je la tapais, mais tendrement pour ainsi dire. Elle criait un peu. Je fermais les volets et a finissait comme toujours. Mais maintenant, cest srieux. Et pour moi, je lai pas assez punie. 

Il ma expliqu alors que ctait pour cela quil avait besoin dun conseil. Il sest arrt pour rgler la mche de la lampe qui charbonnait. Moi, je lcoutais toujours. Javais bu prs dun litre de vin et javais trs chaud aux tempes. Je fumais les cigarettes de Raymond parce quil ne men restait plus. Les derniers trams passaient et emportaient avec eux les bruits maintenant lointains du faubourg. Raymond a continu. Ce qui lennuyait,  cest quil avait encore un sentiment pour son cot . Mais il voulait la punir. Il avait dabord pens  lemmener dans un htel et  appeler les  murs  pour causer un scandale et la faire mettre en carte. Ensuite, il stait adress  des amis quil avait dans le milieu. Ils navaient rien trouv. Et comme me le faisait remarquer Raymond, ctait bien la peine dtre du milieu. Il le leur avait dit et ils avaient alors propos de la  marquer . Mais ce ntait pas ce quil voulait. Il allait rflchir. Auparavant il voulait me demander quelque chose. Dailleurs, avant de me le demander, il voulait savoir ce que je pensais de cette histoire. Jai rpondu que je nen pensais rien mais que ctait intressant. Il ma demand si je pensais quil y avait de la tromperie, et moi, il me semblait bien quil y avait de la tromperie, si je trouvais quon devait la punir et ce que je ferais  sa place, je lui ai dit quon ne pouvait jamais savoir, mais je comprenais quil veuille la punir. Jai encore bu un peu de vin. Il a allum une cigarette et il ma dcouvert son ide. Il voulait lui crire une lettre  avec des coups de pied et en mme temps des choses pour la faire regretter . Aprs, quand elle reviendrait, il coucherait avec elle et  juste au moment de finir  il lui cracherait  la figure et il la mettrait dehors. Jai trouv quen effet, de cette faon, elle serait punie. Mais Raymond ma dit quil ne se sentait pas capable de faire la lettre quil fallait et quil avait pens  moi pour la rdiger. Comme je ne disais rien, il ma demand si cela mennuierait de le faire tout de suite et jai rpondu que non.

Il sest alors lev aprs avoir bu un verre de vin. Il a repouss les assiettes et le peu de boudin froid que nous avions laiss. Il a soigneusement essuy la toile cire de la table. Il a pris dans un tiroir de sa table de nuit une feuille de papier quadrill, une enveloppe jaune, un petit porte-plume de bois rouge et un encrier carr dencre violette. Quand il ma dit le nom de la femme, jai vu que ctait une Mauresque. Jai fait la lettre. Je lai crite un peu au hasard, mais je me suis appliqu  contenter Raymond parce que je navais pas de raison de ne pas le contenter. Puis jai lu la lettre  haute voix. Il ma cout en fumant et en hochant la tte, puis il ma demand de la relire. Il a t tout  fait content. Il ma dit :  Je savais bien que tu connaissais la vie.  Je ne me suis pas aperu dabord quil me tutoyait. Cest seulement quand il ma dclar :  Maintenant, tu es un vrai copain , que cela ma frapp. Il a rpt sa phrase et jai dit :  Oui.  Cela mtait gal dtre son copain et il avait vraiment lair den avoir envie. Il a cachet la lettre et nous avons fini le vin. Puis nous sommes rests un moment  fumer sans rien dire. Au-dehors, tout tait calme, nous avons entendu le glissement dune auto qui passait. Jai dit :  Il est tard.  Raymond le pensait aussi. Il a remarqu que le temps passait vite et, dans un sens, ctait vrai. Javais sommeil, mais javais de la peine  me lever. Jai d avoir lair fatigu parce que Raymond ma dit quil ne fallait pas se laisser aller. Dabord, je nai pas compris. Il ma expliqu alors quil avait appris la mort de maman mais que ctait une chose qui devait arriver un jour ou lautre. Ctait aussi mon avis.

Je me suis lev, Raymond ma serr la main trs fort et ma dit quentre hommes on se comprenait toujours. En sortant de chez lui, jai referm la porte et je suis rest un moment dans le noir, sur le palier. La maison tait calme et des profondeurs de la cage descalier montait un souffle obscur et humide. Je nentendais que les coups de mon sang qui bourdonnait  mes oreilles. Je suis rest immobile. Mais dans la chambre du vieux Salamano, le chien a gmi sourdement.

IV
Jai bien travaill toute la semaine, Raymond est venu et ma dit quil avait envoy la lettre. Je suis all au cinma deux fois avec Emmanuel qui ne comprend pas toujours ce qui se passe sur lcran. Il faut alors lui donner des explications. Hier, ctait samedi et Marie est venue, comme nous en tions convenus. Jai eu trs envie delle parce quelle avait une belle robe  raies rouges et blanches et des sandales de cuir. On devinait ses seins durs et le brun du soleil lui faisait un visage de fleur. Nous avons pris un autobus et nous sommes alls  quelques kilomtres dAlger, sur une plage resserre entre des rochers et borde de roseaux du ct de la terre. Le soleil de quatre heures ntait pas trop chaud, mais leau tait tide, avec de petites vagues longues et paresseuses. Marie ma appris un jeu. Il fallait, en nageant, boire  la crte des vagues, accumuler dans sa bouche toute lcume et se mettre ensuite sur le dos pour la projeter contre le ciel. Cela faisait alors une dentelle mousseuse qui disparaissait dans lair ou me retombait en pluie tide sur le visage. Mais au bout de quelque temps, javais la bouche brle par lamertume du sel. Marie ma rejoint alors et sest colle  moi dans leau. Elle a mis sa bouche contre la mienne. Sa langue rafrachissait mes lvres et nous nous sommes rouls dans les vagues pendant un moment.

Quand nous nous sommes rhabills sur la plage, Marie me regardait avec des yeux brillants. Je lai embrasse.  partir de ce moment, nous navons plus parl. Je lai tenue contre moi et nous avons t presss de trouver un autobus, de rentrer, daller chez moi et de nous jeter sur mon lit. Javais laiss ma fentre ouverte et ctait bon de sentir la nuit dt couler sur nos corps bruns.

Ce matin, Marie est reste et je lui ai dit que nous djeunerions ensemble. Je suis descendu pour acheter de la viande. En remontant, jai entendu une voix de femme dans la chambre de Raymond. Un peu aprs, le vieux Salamano a grond son chien, nous avons entendu un bruit de semelles et de griffes sur les marches en bois de lescalier et puis :  Salaud, charogne , ils sont sortis dans la rue. Jai racont  Marie lhistoire du vieux et elle a ri. Elle avait un de mes pyjamas dont elle avait retrouss les manches. Quand elle a ri, jai eu encore envie delle. Un moment aprs, elle ma demand si je laimais. Je lui ai rpondu que cela ne voulait rien dire, mais quil me semblait que non. Elle a eu lair triste. Mais en prparant le djeuner, et  propos de rien, elle a encore ri de telle faon que je lai embrasse. Cest  ce moment que les bruits dune dispute ont clat chez Raymond.

On a dabord entendu une voix aigu de femme et puis Raymond qui disait :  Tu mas manqu, tu mas manqu. Je vais tapprendre  me manquer.  Quelques bruits sourds et la femme a hurl, mais de si terrible faon quimmdiatement le palier sest empli de monde. Marie et moi nous sommes sortis aussi. La femme criait toujours et Raymond frappait toujours. Marie ma dit que ctait terrible et je nai rien rpondu. Elle ma demand daller chercher un agent, mais je lui ai dit que je naimais pas les agents. Pourtant, il en est arriv un avec le locataire du deuxime qui est plombier. Il a frapp  la porte et on na plus rien entendu. Il a frapp plus fort et au bout dun moment, la femme a pleur et Raymond a ouvert. Il avait une cigarette  la bouche et lair doucereux. La fille sest prcipite  la porte et a dclar  lagent que Raymond lavait frappe.  Ton nom , a dit lagent. Raymond a rpondu.  Enlve ta cigarette de la bouche quand tu me parles , a dit lagent. Raymond a hsit, ma regard et a tir sur sa cigarette.  ce moment, lagent la gifl  toute vole dune claque paisse et lourde, en pleine joue. La cigarette est tombe quelques mtres plus loin. Raymond a chang de visage, mais il na rien dit sur le moment et puis il a demand dune voix humble sil pouvait ramasser son mgot. Lagent a dclar quil le pouvait et il a ajout :  Mais la prochaine fois, tu sauras quun agent nest pas un guignol.  Pendant ce temps, la fille pleurait et elle a rpt :  Il ma tape. Cest un maquereau.    Monsieur lagent, a demand alors Raymond, cest dans la loi, a, de dire maquereau  un homme ?  Mais lagent lui a ordonn  de fermer sa gueule . Raymond sest alors retourn vers la fille et il lui a dit :  Attends, petite, on se retrouvera.  Lagent lui a dit de fermer a, que la fille devait partir et lui rester dans sa chambre en attendant dtre convoqu au commissariat. Il a ajout que Raymond devrait avoir honte dtre sol au point de trembler comme il le faisait.  ce moment, Raymond lui a expliqu :  Je ne suis pas sol, monsieur lagent. Seulement, je suis l, devant vous, et je tremble, cest forc.  Il a ferm sa porte et tout le monde est parti. Marie et moi avons fini de prparer le djeuner. Mais elle navait pas faim, jai presque tout mang. Elle est partie  une heure et jai dormi un peu.

Vers trois heures, on a frapp  ma porte et Raymond est entr. Je suis rest couch. Il sest assis sur le bord de mon lit. Il est rest un moment sans parler et je lui ai demand comment son affaire stait passe. Il ma racont quil avait fait ce quil voulait mais quelle lui avait donn une gifle et qualors il lavait battue. Pour le reste, je lavais vu. Je lui ai dit quil me semblait que maintenant elle tait punie et quil devait tre content. Ctait aussi son avis, et il a observ que lagent avait beau faire, il ne changerait rien aux coups quelle avait reus. Il a ajout quil connaissait bien les agents et quil savait comment il fallait sy prendre avec eux. Il ma demand alors si javais attendu quil rponde  la gifle de lagent. Jai rpondu que je nattendais rien du tout et que dailleurs je naimais pas les agents. Raymond a eu lair trs content. Il ma demand si je voulais sortir avec lui. Je me suis lev et jai commenc  me peigner. Il ma dit quil fallait que je lui serve de tmoin. Moi cela mtait gal, mais je ne savais pas ce que je devais dire. Selon Raymond, il suffisait de dclarer que la fille lui avait manqu. Jai accept de lui servir de tmoin.

Nous sommes sortis et Raymond ma offert une fine. Puis il a voulu faire une partie de billard et jai perdu de justesse. Il voulait ensuite aller au bordel, mais jai dit non parce que je naime pas a. Alors nous sommes rentrs doucement et il me disait combien il tait content davoir russi  punir sa matresse. Je le trouvais trs gentil avec moi et jai pens que ctait un bon moment.

De loin, jai aperu sur le pas de la porte le vieux Salamano qui avait lair agit. Quand nous nous sommes rapprochs, jai vu quil navait pas son chien. Il regardait de tous les cts, tournait sur lui-mme, tentait de percer le noir du couloir, marmonnait des mots sans suite et recommenait  fouiller la rue de ses petits yeux rouges. Quand Raymond lui a demand ce quil avait, il na pas rpondu tout de suite. Jai vaguement entendu quil murmurait :  Salaud, charogne , et il continuait  sagiter. Je lui ai demand o tait son chien. Il ma rpondu brusquement quil tait parti. Et puis tout dun coup, il a parl avec volubilit :  Je lai emmen au Champ de Manuvres, comme dhabitude. Il y avait du monde, autour des baraques foraines. Je me suis arrt pour regarder  le Roi de lvasion . Et quand jai voulu repartir, il ntait plus l. Bien sr, il y a longtemps que je voulais lui acheter un collier moins grand. Mais je naurais jamais cru que cette charogne pourrait partir comme a. 

Raymond lui a expliqu alors que le chien avait pu sgarer et quil allait revenir. Il lui a cit des exemples de chiens qui avaient fait des dizaines de kilomtres pour retrouver leur matre Malgr cela, le vieux a eu lair plus agit.  Mais ils me le prendront, vous comprenez. Si encore quelquun le recueillait. Mais ce nest pas possible, il dgote tout le monde avec ses crotes. Les agents le prendront, cest sr.  Je lui ai dit alors quil devait aller  la fourrire et quon le lui rendrait moyennant le paiement de quelques droits. Il ma demand si ces droits taient levs. Je ne savais pas. Alors, il sest mis en colre :  Donner de largent pour cette charogne. Ah ! il peut bien crever !  Et il sest mis  linsulter. Raymond a ri et a pntr dans la maison. Je lai suivi et nous nous sommes quitts sur le palier de ltage. Un moment aprs, jai entendu le pas du vieux et il a frapp  ma porte. Quand jai ouvert, il est rest un moment sur le seuil et il ma dit :  Excusez-moi, excusez-moi.  Je lai invit  entrer, mais il na pas voulu. Il regardait la pointe de ses souliers et ses mains croteuses tremblaient. Sans me faire face, il ma demand :  Ils ne vont pas me le prendre, dites, monsieur Meursault. Ils vont me le rendre. Ou quest-ce que je vais devenir ?  Je lui ai dit que la fourrire gardait les chiens trois jours  la disposition de leurs propritaires et quensuite elle en faisait ce que bon lui semblait. Il ma regard en silence. Puis il ma dit :  Bonsoir.  Il a ferm sa porte et je lai entendu aller et venir. Son lit a craqu. Et au bizarre petit bruit qui a travers la cloison, jai compris quil pleurait. Je ne sais pas pourquoi jai pens  maman. Mais il fallait que je me lve tt le lendemain. Je navais pas faim et je me suis couch sans dner.

V
Raymond ma tlphon au bureau. Il ma dit quun de ses amis (il lui avait parl de moi) minvitait  passer la journe de dimanche dans son cabanon, prs dAlger. Jai rpondu que je le voulais bien, mais que javais promis ma journe  une amie. Raymond ma tout de suite dclar quil linvitait aussi. La femme de son ami serait trs contente de ne pas tre seule au milieu dun groupe dhommes.

Jai voulu raccrocher tout de suite parce que je sais que le patron naime pas quon nous tlphone de la ville. Mais Raymond ma demand dattendre et il ma dit quil aurait pu me transmettre cette invitation le soir, mais quil voulait mavertir dautre chose. Il avait t suivi toute la journe par un groupe dArabes parmi lesquels se trouvait le frre de son ancienne matresse.  Si tu le vois prs de la maison ce soir en rentrant, avertis-moi.  Jai dit que ctait entendu.

Peu aprs, le patron ma fait appeler et, sur le moment, jai t ennuy parce que jai pens quil allait me dire de moins tlphoner et de mieux travailler. Ce ntait pas cela du tout. Il ma dclar quil allait me parler dun projet encore trs vague. Il voulait seulement avoir mon avis sur la question. Il avait lintention dinstaller un bureau  Paris qui traiterait ses affaires sur la place, et directement, avec les grandes compagnies et il voulait savoir si jtais dispos  y aller. Cela me permettrait de vivre  Paris et aussi de voyager une partie de lanne.  Vous tes jeune, et il me semble que cest une vie qui doit vous plaire.  Jai dit que oui mais que dans le fond cela mtait gal. Il ma demand alors si je ntais pas intress par un changement de vie. Jai rpondu quon ne changeait jamais de vie, quen tout cas toutes se valaient et que la mienne ici ne me dplaisait pas du tout. Il a eu lair mcontent, ma dit que je rpondais toujours  ct, que je navais pas dambition et que cela tait dsastreux dans les affaires. Je suis retourn travailler alors. Jaurais prfr ne pas le mcontenter, mais je ne voyais pas de raison pour changer ma vie. En y rflchissant bien, je ntais pas malheureux. Quand jtais tudiant, javais beaucoup dambitions de ce genre. Mais quand jai d abandonner mes tudes, jai trs vite compris que tout cela tait sans importance relle.

Le soir, Marie est venue me chercher et ma demand si je voulais me marier avec elle. Jai dit que cela mtait gal et que nous pourrions le faire si elle le voulait. Elle a voulu savoir alors si je laimais. Jai rpondu comme je lavais dj fait une fois, que cela ne signifiait rien mais que sans doute je ne laimais pas.  Pourquoi mpouser alors ?  a-t-elle dit. Je lui ai expliqu que cela navait aucune importance et que si elle le dsirait, nous pouvions nous marier. Dailleurs, ctait elle qui le demandait et moi je me contentais de dire oui. Elle a observ alors que le mariage tait une chose grave. Jai rpondu :  Non.  Elle sest tue un moment et elle ma regard en silence. Puis elle a parl. Elle voulait simplement savoir si jaurais accept la mme proposition venant dune autre femme,  qui je serais attach de la mme faon. Jai dit :  Naturellement.  Elle sest demand alors si elle maimait et moi, je ne pouvais rien savoir sur ce point. Aprs un autre moment de silence, elle a murmur que jtais bizarre, quelle maimait sans doute  cause de cela mais que peut-tre un jour je la dgoterais pour les mmes raisons. Comme je me taisais, nayant rien  ajouter, elle ma pris le bras en souriant et elle a dclar quelle voulait se marier avec moi. Jai rpondu que nous le ferions ds quelle le voudrait. Je lui ai parl alors de la proposition du patron et Marie ma dit quelle aimerait connatre Paris. Je lui ai appris que jy avais vcu dans un temps et elle ma demand comment ctait. Je lui ai dit :  Cest sale. Il y a des pigeons et des cours noires. Les gens ont la peau blanche. 

Puis nous avons march et travers la ville par ses grandes rues. Les femmes taient belles et jai demand  Marie si elle le remarquait. Elle ma dit que oui et quelle me comprenait. Pendant un moment, nous navons plus parl. Je voulais cependant quelle reste avec moi et je lui ai dit que nous pouvions dner ensemble chez Cleste. Elle en avait bien envie, mais elle avait  faire. Nous tions prs de chez moi et je lui ai dit au revoir. Elle ma regard :  Tu ne veux pas savoir ce que jai  faire ?  Je voulais bien le savoir, mais je ny avais pas pens et cest ce quelle avait lair de me reprocher. Alors, devant mon air emptr, elle a encore ri et elle a eu vers moi un mouvement de tout le corps pour me tendre sa bouche.

Jai dn chez Cleste. Javais dj commenc  manger lorsquil est entr une bizarre petite femme qui ma demand si elle pouvait sasseoir  ma table. Naturellement, elle le pouvait. Elle avait des gestes saccads et des yeux brillants dans une petite figure de pomme. Elle sest dbarrasse de sa jaquette, sest assise et a consult fivreusement la carte. Elle a appel Cleste et a command immdiatement tous ses plats dune voix  la fois prcise et prcipite. En attendant les hors-duvre, elle a ouvert son sac, en a sorti un petit carr de papier et un crayon, a fait davance laddition, puis a tir dun gousset, augmente du pourboire, la somme exacte quelle a place devant elle.  ce moment, on lui a apport des hors-duvre quelle a engloutis  toute vitesse. En attendant le plat suivant, elle a encore sorti de son sac un crayon bleu et un magazine qui donnait les programmes radiophoniques de la semaine. Avec beaucoup de soin, elle a coch une  une presque toutes les missions. Comme le magazine avait une douzaine de pages, elle a continu ce travail mticuleusement pendant tout le repas. Javais dj fini quelle cochait encore avec la mme application. Puis elle sest leve, a remis sa jaquette avec les mmes gestes prcis dautomate et elle est partie. Comme je navais rien  faire, je suis sorti aussi et je lai suivie un moment. Elle stait place sur la bordure du trottoir et avec une vitesse et une sret incroyables, elle suivait son chemin sans dvier et sans se retourner. Jai fini par la perdre de vue et par revenir sur mes pas. Jai pens quelle tait bizarre, mais je lai oublie assez vite.

Sur le pas de ma porte, jai trouv le vieux Salamano. Je lai fait entrer et il ma appris que son chien tait perdu, car il ntait pas  la fourrire. Les employs lui avaient dit que, peut-tre, il avait t cras. Il avait demand sil ntait pas possible de le savoir dans les commissariats. On lui avait rpondu quon ne gardait pas trace de ces choses-l, parce quelles arrivaient tous les jours. Jai dit au vieux Salamano quil pourrait avoir un autre chien, mais il a eu raison de me faire remarquer quil tait habitu  celui-l.

Jtais accroupi sur mon lit et Salamano stait assis sur une chaise devant la table. Il me faisait face et il avait ses deux mains sur les genoux. Il avait gard son vieux feutre. Il mchonnait des bouts de phrases sous sa moustache jaunie. Il mennuyait un peu, mais je navais rien  faire et je navais pas sommeil. Pour dire quelque chose, je lai interrog sur son chien. Il ma dit quil lavait eu aprs la mort de sa femme. Il stait mari assez tard. Dans sa jeunesse, il avait eu envie de faire du thtre : au rgiment il jouait dans les vaudevilles militaires. Mais finalement, il tait entr dans les chemins de fer et il ne le regrettait pas, parce que maintenant il avait une petite retraite. Il navait pas t heureux avec sa femme, mais dans lensemble il stait bien habitu  elle. Quand elle tait morte, il stait senti trs seul. Alors, il avait demand un chien  un camarade datelier et il avait eu celui-l trs jeune. Il avait fallu le nourrir au biberon. Mais comme un chien vit moins quun homme, ils avaient fini par tre vieux ensemble.  Il avait mauvais caractre, ma dit Salamano. De temps en temps, on avait des prises de bec. Mais ctait un bon chien quand mme.  Jai dit quil tait de belle race et Salamano a eu lair content.  Et encore, a-t-il ajout, vous ne lavez pas connu avant sa maladie. Ctait le poil quil avait de plus beau.  Tous les soirs et tous les matins, depuis que le chien avait eu cette maladie de peau, Salamano le passait  la pommade. Mais selon lui, sa vraie maladie, ctait la vieillesse, et la vieillesse ne se gurit pas.

 ce moment, jai bill et le vieux ma annonc quil allait partir. Je lui ai dit quil pouvait rester, et que jtais ennuy de ce qui tait arriv  son chien : il ma remerci. Il ma dit que maman aimait beaucoup son chien. En parlant delle, il lappelait  votre pauvre mre . Il a mis la supposition que je devais tre bien malheureux depuis que maman tait morte et je nai rien rpondu. Il ma dit alors, trs vite et avec un air gn, quil savait que dans le quartier on mavait mal jug parce que javais mis ma mre  lasile, mais il me connaissait et il savait que jaimais beaucoup maman. Jai rpondu, je ne sais pas encore pourquoi, que jignorais jusquici quon me juget mal  cet gard, mais que lasile mavait paru une chose naturelle puisque je navais pas assez dargent pour faire garder maman.  Dailleurs, ai-je ajout, il y avait longtemps quelle navait rien  me dire et quelle sennuyait toute seule.  Oui, ma-t-il dit, et  lasile, du moins, on se fait des camarades.  Puis il sest excus. Il voulait dormir. Sa vie avait chang maintenant et il ne savait pas trop ce quil allait faire. Pour la premire fois depuis que je le connaissais, dun geste furtif, il ma tendu la main et jai senti les cailles de sa peau. Il a souri un peu et avant de partir, il ma dit :  Jespre que les chiens naboieront pas cette nuit. Je crois toujours que cest le mien. 

VI
Le dimanche, jai eu de la peine  me rveiller et il a fallu que Marie mappelle et me secoue. Nous navons pas mang parce que nous voulions nous baigner tt. Je me sentais tout  fait vide et javais un peu mal  la tte. Ma cigarette avait un got amer. Marie sest moque de moi parce quelle disait que javais  une tte denterrement . Elle avait mis une robe de toile blanche et lch ses cheveux. Je lui ai dit quelle tait belle, elle a ri de plaisir.

En descendant, nous avons frapp  la porte de Raymond. Il nous a rpondu quil descendait. Dans la rue,  cause de ma fatigue et aussi parce que nous navions pas ouvert les persiennes, le jour, dj tout plein de soleil, ma frapp comme une gifle. Marie sautait de joie et narrtait pas de dire quil faisait beau. Je me suis senti mieux et je me suis aperu que javais faim. Je lai dit  Marie qui ma montr son sac en toile cire o elle avait mis nos deux maillots et une serviette. Je navais plus qu attendre et nous avons entendu Raymond fermer sa porte. Il avait un pantalon bleu et une chemise blanche  manches courtes. Mais il avait mis un canotier, ce qui a fait rire Marie, et ses avant-bras taient trs blancs sous les poils noirs. Jen tais un peu dgot. Il sifflait en descendant et il avait lair trs content. Il ma dit :  Salut, vieux , et il a appel Marie  mademoiselle .

La veille nous tions alls au commissariat et javais tmoign que la fille avait  manqu   Raymond. Il en a t quitte pour un avertissement. On na pas contrl mon affirmation. Devant la porte, nous en avons parl avec Raymond, puis nous avons dcid de prendre lautobus. La plage ntait pas trs loin, mais nous irions plus vite ainsi. Raymond pensait que son ami serait content de nous voir arriver tt. Nous allions partir quand Raymond, tout dun coup, ma fait signe de regarder en face. Jai vu un groupe dArabes adosss  la devanture du bureau de tabac. Ils nous regardaient en silence, mais  leur manire, ni plus ni moins que si nous tions des pierres ou des arbres morts. Raymond ma dit que le deuxime  partir de la gauche tait son type, et il a eu lair proccup. Il a ajout que, pourtant, ctait maintenant une histoire finie. Marie ne comprenait pas trs bien et nous a demand ce quil y avait. Je lui ai dit que ctaient des Arabes qui en voulaient  Raymond. Elle a voulu quon parte tout de suite. Raymond sest redress et il a ri en disant quil fallait se dpcher.

Nous sommes alls vers larrt dautobus qui tait un peu plus loin et Raymond ma annonc que les Arabes ne nous suivaient pas. Je me suis retourn. Ils taient toujours  la mme place et ils regardaient avec la mme indiffrence lendroit que nous venions de quitter. Nous avons pris lautobus. Raymond, qui paraissait tout  fait soulag, narrtait pas de faire des plaisanteries pour Marie. Jai senti quelle lui plaisait, mais elle ne lui rpondait presque pas. De temps en temps, elle le regardait en riant.

Nous sommes descendus dans la banlieue dAlger. La plage nest pas loin de larrt dautobus. Mais il a fallu traverser un petit plateau qui domine la mer et qui dvale ensuite vers la plage. Il tait couvert de pierres jauntres et dasphodles tout blancs sur le bleu dj dur du ciel. Marie samusait  en parpiller les ptales  grands coups de son sac de toile cire. Nous avons march entre des files de petites villas  barrires vertes ou blanches, quelques-unes enfouies avec leurs vrandas sous les tamaris, quelques autres nues au milieu des pierres. Avant darriver au bord du plateau, on pouvait voir dj la mer immobile et plus loin un cap somnolent et massif dans leau claire. Un lger bruit de moteur est mont dans lair calme jusqu nous. Et nous avons vu, trs loin, un petit chalutier qui avanait, imperceptiblement, sur la mer clatante. Marie a cueilli quelques iris de roche. De la pente qui descendait vers la mer nous avons vu quil y avait dj quelques baigneurs.

Lami de Raymond habitait un petit cabanon de bois  lextrmit de la plage. La maison tait adosse  des rochers et les pilotis qui la soutenaient sur le devant baignaient dj dans leau. Raymond nous a prsents. Son ami sappelait Masson. Ctait un grand type, massif de taille et dpaules, avec une petite femme ronde et gentille,  laccent parisien. Il nous a dit tout de suite de nous mettre  laise et quil y avait une friture de poissons quil avait pchs le matin mme. Je lui ai dit combien je trouvais sa maison jolie. Il ma appris quil y venait passer le samedi, le dimanche et tous ses jours de cong.  Avec ma femme, on sentend bien , a-t-il ajout. Justement, sa femme riait avec Marie. Pour la premire fois peut-tre, jai pens vraiment que jallais me marier.

Masson voulait se baigner, mais sa femme et Raymond ne voulaient pas venir. Nous sommes descendus tous les trois et Marie sest immdiatement jete dans leau. Masson et moi, nous avons attendu un peu. Lui parlait lentement et jai remarqu quil avait lhabitude de complter tout ce quil avanait par un  et je dirai plus , mme quand, au fond, il najoutait rien au sens de sa phrase.  propos de Marie, il ma dit :  Elle est patante, et je dirai plus, charmante.  Puis je nai plus fait attention  ce tic parce que jtais occup  prouver que le soleil me faisait du bien. Le sable commenait  chauffer sous les pieds. Jai retard encore lenvie que javais de leau, mais jai fini par dire  Masson :  On y va ?  Jai plong. Lui est entr dans leau doucement et sest jet quand il a perdu pied. Il nageait  la brasse et assez mal, de sorte que je lai laiss pour rejoindre Marie. Leau tait froide et jtais content de nager. Avec Marie, nous nous sommes loigns et nous nous sentions daccord dans nos gestes et dans notre contentement.

Au large, nous avons fait la planche et sur mon visage tourn vers le ciel le soleil cartait les derniers voiles deau qui me coulaient dans la bouche. Nous avons vu que Masson regagnait la plage pour stendre au soleil. De loin, il paraissait norme. Marie a voulu que nous nagions ensemble. Je me suis mis derrire elle pour la prendre par la taille et elle avanait  la force des bras pendant que je laidais en battant des pieds. Le petit bruit de leau battue nous a suivis dans le matin jusqu ce que je me sente fatigu. Alors jai laiss Marie et je suis rentr en nageant rgulirement et en respirant bien. Sur la plage, je me suis tendu  plat ventre prs de Masson et jai mis ma figure dans le sable. Je lui ai dit que  ctait bon  et il tait de cet avis. Peu aprs, Marie est venue. Je me suis retourn pour la regarder avancer. Elle tait toute visqueuse deau sale et elle tenait ses cheveux en arrire. Elle sest allonge flanc  flanc avec moi et les deux chaleurs de son corps et du soleil mont un peu endormi.

Marie ma secou et ma dit que Masson tait remont chez lui, il fallait djeuner. Je me suis lev tout de suite parce que javais faim, mais Marie ma dit que je ne lavais pas embrasse depuis ce matin. Ctait vrai et pourtant jen avais envie.  Viens dans leau , ma-t-elle dit. Nous avons couru pour nous taler dans les premires petites vagues. Nous avons fait quelques brasses et elle sest colle contre moi. Jai senti ses jambes autour des miennes et je lai dsire.

Quand nous sommes revenus, Masson nous appelait dj. Jai dit que javais trs faim et il a dclar tout de suite  sa femme que je lui plaisais. Le pain tait bon, jai dvor ma part de poisson. Il y avait ensuite de la viande et des pommes de terre frites. Nous mangions tous sans parler. Masson buvait souvent du vin et il me servait sans arrt. Au caf, javais la tte un peu lourde et jai fum beaucoup. Masson, Raymond et moi, nous avons envisag de passer ensemble le mois daot  la plage,  frais communs. Marie nous a dit tout dun coup :  Vous savez quelle heure il est ? Il est onze heures et demie.  Nous tions tous tonns, mais Masson a dit quon avait mang trs tt, et que ctait naturel parce que lheure du djeuner, ctait lheure o lon avait faim. Je ne sais pas pourquoi cela a fait rire Marie. Je crois quelle avait un peu trop bu. Masson ma demand alors si je voulais me promener sur la plage avec lui.  Ma femme fait toujours la sieste aprs le djeuner. Moi, je naime pas a. Il faut que je marche. Je lui dis toujours que cest meilleur pour la sant. Mais aprs tout, cest son droit.  Marie a dclar quelle resterait pour aider Mme Masson  faire la vaisselle. La petite Parisienne a dit que pour cela, il fallait mettre les hommes dehors. Nous sommes descendus tous les trois.

Le soleil tombait presque daplomb sur le sable et son clat sur la mer tait insoutenable. Il ny avait plus personne sur la plage. Dans les cabanons qui bordaient le plateau et qui surplombaient la mer, on entendait des bruits dassiettes et de couverts. On respirait  peine dans la chaleur de pierre qui montait du sol. Pour commencer, Raymond et Masson ont parl de choses et de gens que je ne connaissais pas. Jai compris quil y avait longtemps quils se connaissaient et quils avaient mme vcu ensemble  un moment. Nous nous sommes dirigs vers leau et nous avons long la mer. Quelquefois, une petite vague plus longue que lautre venait mouiller nos souliers de toile. Je ne pensais  rien parce que jtais  moiti endormi par ce soleil sur ma tte nue.

 ce moment, Raymond a dit  Masson quelque chose que jai mal entendu. Mais jai aperu en mme temps, tout au bout de la plage et trs loin de nous, deux Arabes en bleu de chauffe qui venaient dans notre direction. Jai regard Raymond et il ma dit :  Cest lui.  Nous avons continu  marcher. Masson a demand comment ils avaient pu nous suivre jusque-l. Jai pens quils avaient d nous voir prendre lautobus avec un sac de plage, mais je nai rien dit.

Les Arabes avanaient lentement et ils taient dj beaucoup plus rapprochs. Nous navons pas chang notre allure, mais Raymond a dit :  Sil y a de la bagarre, toi, Masson, tu prendras le deuxime. Moi, je me charge de mon type. Toi, Meursault, sil en arrive un autre, il est pour toi.  Jai dit :  Oui  et Masson a mis ses mains dans les poches. Le sable surchauff me semblait rouge maintenant. Nous avancions dun pas gal vers les Arabes. La distance entre nous a diminu rgulirement. Quand nous avons t  quelques pas les uns des autres, les Arabes se sont arrts. Masson et moi nous avons ralenti notre pas. Raymond est all tout droit vers son type. Jai mal entendu ce quil lui a dit, mais lautre a fait mine de lui donner un coup de tte. Raymond a frapp alors une premire fois et il a tout de suite appel Masson. Masson est all  celui quon lui avait dsign et il a frapp deux fois avec tout son poids. LArabe sest aplati dans leau, la face contre le fond, et il est rest quelques secondes ainsi, des bulles crevant  la surface, autour de sa tte. Pendant ce temps Raymond aussi a frapp et lautre avait la figure en sang. Raymond sest retourn vers moi et a dit :  Tu vas voir ce quil va prendre.  Je lui ai cri :  Attention, il a un couteau !  Mais dj Raymond avait le bras ouvert et la bouche taillade.

Masson a fait un bond en avant. Mais lautre Arabe stait relev et il sest plac derrire celui qui tait arm. Nous navons pas os bouger. Ils ont recul lentement, sans cesser de nous regarder et de nous tenir en respect avec le couteau. Quand ils ont vu quils avaient assez de champ, ils se sont enfuis trs vite, pendant que nous restions clous sous le soleil et que Raymond tenait serr son bras dgouttant de sang.

Masson a dit immdiatement quil y avait un docteur qui passait ses dimanches sur le plateau. Raymond a voulu y aller tout de suite. Mais chaque fois quil parlait, le sang de sa blessure faisait des bulles dans sa bouche. Nous lavons soutenu et nous sommes revenus au cabanon aussi vite que possible. L, Raymond a dit que ses blessures taient superficielles et quil pouvait aller chez le docteur. Il est parti avec Masson et je suis rest pour expliquer aux femmes ce qui tait arriv. Mme Masson pleurait et Marie tait trs ple. Moi, cela mennuyait de leur expliquer. Jai fini par me taire et jai fum en regardant la mer.

Vers une heure et demie, Raymond est revenu avec Masson. Il avait le bras band et du sparadrap au coin de la bouche. Le docteur lui avait dit que ce ntait rien, mais Raymond avait lair trs sombre. Masson a essay de le faire rire. Mais il ne parlait toujours pas. Quand il a dit quil descendait sur la plage, je lui ai demand o il allait. Il ma rpondu quil voulait prendre lair. Masson et moi avons dit que nous allions laccompagner. Alors, il sest mis en colre et nous a insults. Masson a dclar quil ne fallait pas le contrarier. Moi, je lai suivi quand mme.

Nous avons march longtemps sur la plage. Le soleil tait maintenant crasant. Il se brisait en morceaux sur le sable et sur la mer. Jai eu limpression que Raymond savait o il allait, mais ctait sans doute faux. Tout au bout de la plage, nous sommes arrivs enfin  une petite source qui coulait dans le sable, derrire un gros rocher. L, nous avons trouv nos deux Arabes. Ils taient couchs, dans leurs bleus de chauffe graisseux. Ils avaient lair tout  fait calmes et presque contents. Notre venue na rien chang. Celui qui avait frapp Raymond le regardait sans rien dire. Lautre soufflait dans un petit roseau et rptait sans cesse, en nous regardant du coin de lil, les trois notes quil obtenait de son instrument.

Pendant tout ce temps, il ny a plus eu que le soleil et ce silence, avec le petit bruit de la source et les trois notes. Puis Raymond a port la main  sa poche revolver, mais lautre na pas boug et ils se regardaient toujours. Jai remarqu que celui qui jouait de la flte avait les doigts des pieds trs carts. Mais sans quitter des yeux son adversaire, Raymond ma demand :  Je le descends ?  Jai pens que si je disais non il sexciterait tout seul et tirerait certainement. Je lui ai seulement dit :  Il ne ta pas encore parl. a ferait vilain de tirer comme a.  On a encore entendu le petit bruit deau et de flte au cur du silence et de la chaleur. Puis Raymond a dit :  Alors, je vais linsulter et quand il rpondra, je le descendrai.  Jai rpondu :  Cest a. Mais sil ne sort pas son couteau, tu ne peux pas tirer.  Raymond a commenc  sexciter un peu. Lautre jouait toujours et tous deux observaient chaque geste de Raymond.  Non, ai-je dit  Raymond. Prends-le dhomme  homme et donne-moi ton revolver. Si lautre intervient, ou sil tire son couteau, je le descendrai. 

Quand Raymond ma donn son revolver, le soleil a gliss dessus. Pourtant, nous sommes rests encore immobiles comme si tout stait referm autour de nous. Nous nous regardions sans baisser les yeux et tout sarrtait ici entre la mer, le sable et le soleil, le double silence de la flte et de leau. Jai pens  ce moment quon pouvait tirer ou ne pas tirer. Mais brusquement, les Arabes,  reculons, se sont couls derrire le rocher. Raymond et moi sommes alors revenus sur nos pas. Lui paraissait mieux et il a parl de lautobus du retour.

Je lai accompagn jusquau cabanon et, pendant quil gravissait lescalier de bois, je suis rest devant la premire marche, la tte retentissante de soleil, dcourag devant leffort quil fallait faire pour monter ltage de bois et aborder encore les femmes. Mais la chaleur tait telle quil mtait pnible aussi de rester immobile sous la pluie aveuglante qui tombait du ciel. Rester ici ou partir, cela revenait au mme. Au bout dun moment, je suis retourn vers la plage et je me suis mis  marcher.

Ctait le mme clatement rouge. Sur le sable, la mer haletait de toute la respiration rapide et touffe de ses petites vagues. Je marchais lentement vers les rochers et je sentais mon front se gonfler sous le soleil. Toute cette chaleur sappuyait sur moi et sopposait  mon avance. Et chaque fois que je sentais son grand souffle chaud sur mon visage, je serrais les dents, je fermais les poings dans les poches de mon pantalon, je me tendais tout entier pour triompher du soleil et de cette ivresse opaque quil me dversait.  chaque pe de lumire jaillie du sable, dun coquillage blanchi ou dun dbris de verre, mes mchoires se crispaient. Jai march longtemps.

Je voyais de loin la petite masse sombre du rocher entoure dun halo aveuglant par la lumire et la poussire de mer. Je pensais  la source frache derrire le rocher. Javais envie de retrouver le murmure de son eau, envie de fuir le soleil, leffort et les pleurs de femme, envie enfin de retrouver lombre et son repos. Mais quand jai t plus prs, jai vu que le type de Raymond tait revenu.

Il tait seul. Il reposait sur le dos, les mains sous la nuque, le front dans les ombres du rocher, tout le corps au soleil. Son bleu de chauffe fumait dans la chaleur. Jai t un peu surpris. Pour moi, ctait une histoire finie et jtais venu l sans y penser.

Ds quil ma vu, il sest soulev un peu et a mis la main dans sa poche. Moi, naturellement, jai serr le revolver de Raymond dans mon veston. Alors de nouveau, il sest laiss aller en arrire, mais sans retirer la main de sa poche. Jtais assez loin de lui,  une dizaine de mtres. Je devinais son regard par instants, entre ses paupires mi-closes. Mais le plus souvent, son image dansait devant mes yeux, dans lair enflamm. Le bruit des vagues tait encore plus paresseux, plus tale qu midi. Ctait le mme soleil, la mme lumire sur le mme sable qui se prolongeait ici. Il y avait dj deux heures que la journe navanait plus, deux heures quelle avait jet lancre dans un ocan de mtal bouillant.  lhorizon, un petit vapeur est pass et jen ai devin la tache noire au bord de mon regard, parce que je navais pas cess de regarder lArabe.

Jai pens que je navais quun demi-tour  faire et ce serait fini. Mais toute une plage vibrante de soleil se pressait derrire moi. Jai fait quelques pas vers la source. LArabe na pas boug. Malgr tout, il tait encore assez loin. Peut-tre  cause des ombres sur son visage, il avait lair de rire. Jai attendu. La brlure du soleil gagnait mes joues et jai senti des gouttes de sueur samasser dans mes sourcils. Ctait le mme soleil que le jour o javais enterr maman et, comme alors, le front surtout me faisait mal et toutes ses veines battaient ensemble sous la peau.  cause de cette brlure que je ne pouvais plus supporter, jai fait un mouvement en avant. Je savais que ctait stupide, que je ne me dbarrasserais pas du soleil en me dplaant dun pas. Mais jai fait un pas, un seul pas en avant. Et cette fois, sans se soulever, lArabe a tir son couteau quil ma prsent dans le soleil. La lumire a gicl sur lacier et ctait comme une longue lame tincelante qui matteignait au front. Au mme instant, la sueur amasse dans mes sourcils a coul dun coup sur les paupires et les a recouvertes dun voile tide et pais. Mes yeux taient aveugls derrire ce rideau de larmes et de sel. Je ne sentais plus que les cymbales du soleil sur mon front et, indistinctement, le glaive clatant jailli du couteau toujours en face de moi. Cette pe brlante rongeait mes cils et fouillait mes yeux douloureux. Cest alors que tout a vacill. La mer a charri un souffle pais et ardent. Il ma sembl que le ciel souvrait sur toute son tendue pour laisser pleuvoir du feu. Tout mon tre sest tendu et jai crisp ma main sur le revolver. La gchette a cd, jai touch le ventre poli de la crosse et cest l, dans le bruit  la fois sec et assourdissant, que tout a commenc. Jai secou la sueur et le soleil. Jai compris que javais dtruit lquilibre du jour, le silence exceptionnel dune plage o javais t heureux. Alors, jai tir encore quatre fois sur un corps inerte o les balles senfonaient sans quil y part. Et ctait comme quatre coups brefs que je frappais sur la porte du malheur.

DEUXIME PARTIE
I
Tout de suite aprs mon arrestation, jai t interrog plusieurs fois. Mais il sagissait dinterrogatoires didentit qui nont pas dur longtemps. La premire fois au commissariat, mon affaire semblait nintresser personne. Huit jours aprs, le juge dinstruction, au contraire, ma regard avec curiosit. Mais pour commencer, il ma seulement demand mon nom et mon adresse, ma profession, la date et le lieu de ma naissance. Puis il a voulu savoir si javais choisi un avocat. Jai reconnu que non et je lai questionn pour savoir sil tait absolument ncessaire den avoir un.  Pourquoi ?  a-t-il dit. Jai rpondu que je trouvais mon affaire trs simple. Il a souri en disant :  Cest un avis. Pourtant, la loi est l. Si vous ne choisissez pas davocat, nous en dsignerons un doffice.  Jai trouv quil tait trs commode que la justice se charget de ces dtails. Je le lui ai dit. Il ma approuv et a conclu que la loi tait bien faite.

Au dbut, je ne lai pas pris au srieux. Il ma reu dans une pice tendue de rideaux, il avait sur son bureau une seule lampe qui clairait le fauteuil o il ma fait asseoir pendant que lui-mme restait dans lombre. Javais dj lu une description semblable dans des livres et tout cela ma paru un jeu. Aprs notre conversation, au contraire, je lai regard et jai vu un homme aux traits fins, aux yeux bleus enfoncs, grand, avec une longue moustache grise et dabondants cheveux presque blancs. Il ma paru trs raisonnable et, somme toute, sympathique, malgr quelques tics nerveux qui lui tiraient la bouche. En sortant, jallais mme lui tendre la main, mais je me suis souvenu  temps que javais tu un homme.

Le lendemain, un avocat est venu me voir  la prison. Il tait petit et rond, assez jeune, les cheveux soigneusement colls. Malgr la chaleur (jtais en manches de chemise), il avait un costume sombre, un col cass et une cravate bizarre  grosses raies noires et blanches. Il a pos sur mon lit la serviette quil portait sous le bras, sest prsent et ma dit quil avait tudi mon dossier. Mon affaire tait dlicate, mais il ne doutait pas du succs, si je lui faisais confiance. Je lai remerci et il ma dit :  Entrons dans le vif du sujet. 

Il sest assis sur le lit et ma expliqu quon avait pris des renseignements sur ma vie prive. On avait su que ma mre tait morte rcemment  lasile. On avait alors fait une enqute  Marengo. Les instructeurs avaient appris que  javais fait preuve dinsensibilit  le jour de lenterrement de maman.  Vous comprenez, ma dit mon avocat, cela me gne un peu de vous demander cela. Mais cest trs important. Et ce sera un gros argument pour laccusation, si je ne trouve rien  rpondre.  Il voulait que je laide. Il ma demand si javais eu de la peine ce jour-l. Cette question ma beaucoup tonn et il me semblait que jaurais t trs gn si javais eu  la poser. Jai rpondu cependant que javais un peu perdu lhabitude de minterroger et quil mtait difficile de le renseigner. Sans doute, jaimais bien maman, mais cela ne voulait rien dire. Tous les tres sains avaient plus ou moins souhait la mort de ceux quils aimaient. Ici, lavocat ma coup et a paru trs agit. Il ma fait promettre de ne pas dire cela  laudience, ni chez le magistrat instructeur. Cependant, je lui ai expliqu que javais une nature telle que mes besoins physiques drangeaient souvent mes sentiments. Le jour o javais enterr maman, jtais trs fatigu, et javais sommeil. De sorte que je ne me suis pas rendu compte de ce qui se passait. Ce que je pouvais dire  coup sr, cest que jaurais prfr que maman ne mourt pas. Mais mon avocat navait pas lair content. Il ma dit :  Ceci nest pas assez. 

Il a rflchi. Il ma demand sil pouvait dire que ce jour-l javais domin mes sentiments naturels. Je lui ai dit :  Non, parce que cest faux.  Il ma regard dune faon bizarre, comme si je lui inspirais un peu de dgot. Il ma dit presque mchamment que dans tous les cas le directeur et le personnel de lasile seraient entendus comme tmoins et que  cela pouvait me jouer un trs sale tour . Je lui ai fait remarquer que cette histoire navait pas de rapport avec mon affaire, mais il ma rpondu seulement quil tait visible que je navais jamais eu de rapports avec la justice.

Il est parti avec un air fch. Jaurais voulu le retenir, lui expliquer que je dsirais sa sympathie, non pour tre mieux dfendu, mais, si je puis dire, naturellement. Surtout, je voyais que je le mettais mal  laise. Il ne me comprenait pas et il men voulait un peu. Javais le dsir de lui affirmer que jtais comme tout le monde, absolument comme tout le monde. Mais tout cela, au fond, navait pas grande utilit et jy ai renonc par paresse.

Peu de temps aprs, jtais conduit de nouveau devant le juge dinstruction. Il tait deux heures de laprs-midi et cette fois, son bureau tait plein dune lumire  peine tamise par un rideau de voile. Il faisait trs chaud. Il ma fait asseoir et, avec beaucoup de courtoisie, ma dclar que mon avocat,  par suite dun contretemps , navait pu venir. Mais javais le droit de ne pas rpondre  ses questions et dattendre que mon avocat pt massister. Jai dit que je pouvais rpondre seul. Il a touch du doigt un bouton sur la table. Un jeune greffier est venu sinstaller presque dans mon dos.

Nous nous sommes tous les deux carrs dans nos fauteuils. Linterrogatoire a commenc. Il ma dabord dit quon me dpeignait comme tant dun caractre taciturne et renferm et il a voulu savoir ce que jen pensais. Jai rpondu :  Cest que je nai jamais grand-chose  dire. Alors je me tais.  Il a souri comme la premire fois, a reconnu que ctait la meilleure des raisons et a ajout :  Dailleurs, cela na aucune importance.  Il sest tu, ma regard et sest redress assez brusquement pour me dire trs vite :  Ce qui mintresse, cest vous.  Je nai pas bien compris ce quil entendait par l et je nai rien rpondu.  Il y a des choses, a-t-il ajout, qui mchappent dans votre geste. Je suis sr que vous allez maider  les comprendre.  Jai dit que tout tait trs simple. Il ma press de lui retracer ma journe. Je lui ai retrac ce que dj je lui avais racont : Raymond, la plage, le bain, la querelle, encore la plage, la petite source, le soleil et les cinq coups de revolver.  chaque phrase il disait :  Bien, bien.  Quand je suis arriv au corps tendu, il a approuv en disant :  Bon.  Moi, jtais lass de rpter ainsi la mme histoire et il me semblait que je navais jamais autant parl.

Aprs un silence, il sest lev et ma dit quil voulait maider, que je lintressais et quavec laide de Dieu, il ferait quelque chose pour moi. Mais auparavant, il voulait me poser encore quelques questions. Sans transition, il ma demand si jaimais maman. Jai dit :  Oui, comme tout le monde  et le greffier, qui jusquici tapait rgulirement sur sa machine, a d se tromper de touches, car il sest embarrass et a t oblig de revenir en arrire. Toujours sans logique apparente, le juge ma alors demand si javais tir les cinq coups de revolver  la suite. Jai rflchi et prcis que javais tir une seule fois dabord et, aprs quelques secondes, les quatre autres coups.  Pourquoi avez-vous attendu entre le premier et le second coup ?  dit-il alors. Une fois de plus, jai revu la plage rouge et jai senti sur mon front la brlure du soleil. Mais cette fois, je nai rien rpondu. Pendant tout le silence qui a suivi le juge a eu lair de sagiter. Il sest assis, a fourrag dans ses cheveux, a mis ses coudes sur son bureau et sest pench un peu vers moi avec un air trange :  Pourquoi, pourquoi avez-vous tir sur un corps  terre ?  L encore, je nai pas su rpondre. Le juge a pass ses mains sur son front et a rpt sa question dune voix un peu altre :  Pourquoi ? Il faut que vous me le disiez. Pourquoi ?  Je me taisais toujours.

Brusquement, il sest lev, a march  grands pas vers une extrmit de son bureau et a ouvert un tiroir dans un classeur. Il en a tir un crucifix dargent quil a brandi en revenant vers moi. Et dune voix toute change, presque tremblante, il sest cri :  Est-ce que vous le connaissez, celui-l ?  Jai dit :  Oui, naturellement.  Alors il ma dit trs vite et dune faon passionne que lui croyait en Dieu, que sa conviction tait quaucun homme ntait assez coupable pour que Dieu ne lui pardonnt pas, mais quil fallait pour cela que lhomme par son repentir devnt comme un enfant dont lme est vide et prte  tout accueillir. Il avait tout son corps pench sur la table. Il agitait son crucifix presque au-dessus de moi.  vrai dire, je lavais trs mal suivi dans son raisonnement, dabord parce que javais chaud et quil y avait dans son cabinet de grosses mouches qui se posaient sur ma figure, et aussi parce quil me faisait un peu peur. Je reconnaissais en mme temps que ctait ridicule parce que, aprs tout, ctait moi le criminel. Il a continu pourtant. Jai  peu prs compris qu son avis il ny avait quun point dobscur dans ma confession, le fait davoir attendu pour tirer mon second coup de revolver. Pour le reste, ctait trs bien, mais cela, il ne le comprenait pas.

Jallais lui dire quil avait tort de sobstiner : ce dernier point navait pas tellement dimportance. Mais il ma coup et ma exhort une dernire fois, dress de toute sa hauteur, en me demandant si je croyais en Dieu. Jai rpondu que non. Il sest assis avec indignation. Il ma dit que ctait impossible, que tous les hommes croyaient en Dieu, mme ceux qui se dtournaient de son visage. Ctait l sa conviction et, sil devait jamais en douter, sa vie naurait plus de sens.  Voulez-vous, sest-il exclam, que ma vie nait pas de sens ?   mon avis, cela ne me regardait pas et je le lui ai dit. Mais  travers la table, il avanait dj le Christ sous mes yeux et scriait dune faon draisonnable :  Moi, je suis chrtien. Je demande pardon de tes fautes  celui-l. Comment peux-tu ne pas croire quil a souffert pour toi ?  Jai bien remarqu quil me tutoyait, mais jen avais assez. La chaleur se faisait de plus en plus grande. Comme toujours, quand jai envie de me dbarrasser de quelquun que jcoute  peine, jai eu lair dapprouver.  ma surprise, il a triomph :  Tu vois, tu vois, disait-il. Nest-ce pas que tu crois et que tu vas te confier  lui ?  videmment, jai dit non une fois de plus. Il est retomb sur son fauteuil.

Il avait lair trs fatigu. Il est rest un moment silencieux pendant que la machine, qui navait pas cess de suivre le dialogue, en prolongeait encore les dernires phrases. Ensuite, il ma regard attentivement et avec un peu de tristesse. Il a murmur :  Je nai jamais vu dme aussi endurcie que la vtre. Les criminels qui sont venus devant moi ont toujours pleur devant cette image de la douleur.  Jallais rpondre que ctait justement parce quil sagissait de criminels. Mais jai pens que moi aussi jtais comme eux. Ctait une ide  quoi je ne pouvais pas me faire. Le juge sest alors lev, comme sil me signifiait que linterrogatoire tait termin. Il ma seulement demand du mme air un peu las si je regrettais mon acte. Jai rflchi et jai dit que, plutt que du regret vritable, jprouvais un certain ennui. Jai eu limpression quil ne me comprenait pas. Mais ce jour-l les choses ne sont pas alles plus loin.

Par la suite jai souvent revu le juge dinstruction. Seulement, jtais accompagn de mon avocat  chaque fois. On se bornait  me faire prciser certains points de mes dclarations prcdentes. Ou bien encore le juge discutait les charges avec mon avocat. Mais en vrit ils ne soccupaient jamais de moi  ces moments-l. Peu  peu en tout cas, le ton des interrogatoires a chang. Il semblait que le juge ne sintresst plus  moi et quil et class mon cas en quelque sorte. Il ne ma plus parl de Dieu et je ne lai jamais revu dans lexcitation de ce premier jour. Le rsultat, cest que nos entretiens sont devenus plus cordiaux. Quelques questions, un peu de conversation avec mon avocat, les interrogatoires taient finis. Mon affaire suivait son cours, selon lexpression mme du juge. Quelquefois aussi, quand la conversation tait dordre gnral, on my mlait. Je commenais  respirer. Personne, en ces heures-l, ntait mchant avec moi. Tout tait si naturel, si bien rgl et si sobrement jou que javais limpression ridicule de  faire partie de la famille . Et au bout des onze mois qua dur cette instruction, je peux dire que je mtonnais presque de mtre jamais rjoui dautre chose que de ces rares instants o le juge me reconduisait  la porte de son cabinet en me frappant sur lpaule et en me disant dun air cordial :  Cest fini pour aujourdhui, monsieur lAntchrist.  On me remettait alors entre les mains des gendarmes.

II
Il y a des choses dont je nai jamais aim parler. Quand je suis entr en prison, jai compris au bout de quelques jours que je naimerais pas parler de cette partie de ma vie.

Plus tard, je nai plus trouv dimportance  ces rpugnances. En ralit, je ntais pas rellement en prison les premiers jours : jattendais vaguement quelque vnement nouveau. Cest seulement aprs la premire et la seule visite de Marie que tout a commenc. Du jour o jai reu sa lettre (elle me disait quon ne lui permettait plus de venir parce quelle ntait pas ma femme), de ce jour-l, jai senti que jtais chez moi dans ma cellule et que ma vie sy arrtait. Le jour de mon arrestation, on ma dabord enferm dans une chambre o il y avait dj plusieurs dtenus, la plupart des Arabes. Ils ont ri en me voyant. Puis ils mont demand ce que javais fait. Jai dit que javais tu un Arabe et ils sont rests silencieux. Mais un moment aprs, le soir est tomb. Ils mont expliqu comment il fallait arranger la natte o je devais coucher. En roulant une des extrmits, on pouvait en faire un traversin. Toute la nuit, des punaises ont couru sur mon visage. Quelques jours aprs, on ma isol dans une cellule o je couchais sur un bat-flanc de bois. Javais un baquet daisances et une cuvette de fer. La prison tait tout en haut de la ville et, par une petite fentre, je pouvais voir la mer. Cest un jour que jtais agripp aux barreaux, mon visage tendu vers la lumire, quun gardien est entr et ma dit que javais une visite. Jai pens que ctait Marie. Ctait bien elle.

Jai suivi pour aller au parloir un long corridor, puis un escalier et pour finir un autre couloir. Je suis entr dans une trs grande salle claire par une vaste baie. La salle tait spare en trois parties par deux grandes grilles qui la coupaient dans sa longueur. Entre les deux grilles se trouvait un espace de huit  dix mtres qui sparait les visiteurs des prisonniers. Jai aperu Marie en face de moi avec sa robe  raies et son visage bruni. De mon ct, il y avait une dizaine de dtenus, des Arabes pour la plupart. Marie tait entoure de Mauresques et se trouvait entre deux visiteuses : une petite vieille aux lvres serres, habille de noir, et une grosse femme en cheveux qui parlait trs fort avec beaucoup de gestes.  cause de la distance entre les grilles, les visiteurs et les prisonniers taient obligs de parler trs haut. Quand je suis entr, le bruit des voix qui rebondissaient contre les grands murs nus de la salle, la lumire crue qui coulait du ciel sur les vitres et rejaillissait dans la salle, me causrent une sorte dtourdissement. Ma cellule tait plus calme et plus sombre. Il ma fallu quelques secondes pour madapter. Pourtant, jai fini par voir chaque visage avec nettet, dtach dans le plein jour. Jai observ quun gardien se tenait assis  lextrmit du couloir entre les deux grilles. La plupart des prisonniers arabes ainsi que leurs familles staient accroupis en vis--vis. Ceux-l ne criaient pas. Malgr le tumulte, ils parvenaient  sentendre en parlant trs bas. Leur murmure sourd, parti de plus bas, formait comme une basse continue aux conversations qui sentrecroisaient au-dessus de leurs ttes. Tout cela, je lai remarqu trs vite en mavanant vers Marie. Dj colle contre la grille, elle me souriait de toutes ses forces. Je lai trouve trs belle, mais je nai pas su le lui dire.

 Alors ? ma-t-elle dit trs haut.  Alors, voil.  Tu es bien, tu as tout ce que tu veux ?  Oui, tout. 

Nous nous sommes tus et Marie souriait toujours. La grosse femme hurlait vers mon voisin, son mari sans doute, un grand type blond au regard franc. Ctait la suite dune conversation dj commence.

 Jeanne na pas voulu le prendre, criait-elle  tue-tte.  Oui, oui, disait lhomme.  Je lui ai dit que tu le reprendrais en sortant, mais elle na pas voulu le prendre. 

Marie a cri de son ct que Raymond me donnait le bonjour et jai dit :  Merci.  Mais ma voix a t couverte par mon voisin qui a demand  sil allait bien . Sa femme a ri en disant  quil ne stait jamais mieux port . Mon voisin de gauche, un petit jeune homme aux mains fines, ne disait rien. Jai remarqu quil tait en face de la petite vieille et que tous les deux se regardaient avec intensit. Mais je nai pas eu le temps de les observer plus longtemps parce que Marie ma cri quil fallait esprer. Jai dit :  Oui.  En mme temps, je la regardais et javais envie de serrer son paule par-dessus sa robe. Javais envie de ce tissu fin et je ne savais pas trs bien ce quil fallait esprer en dehors de lui. Mais ctait bien sans doute ce que Marie voulait dire parce quelle souriait toujours. Je ne voyais plus que lclat de ses dents et les petits plis de ses yeux. Elle a cri de nouveau :  Tu sortiras et on se mariera !  Jai rpondu :  Tu crois ?  mais ctait surtout pour dire quelque chose. Elle a dit alors trs vite et toujours trs haut que oui, que je serais acquitt et quon prendrait encore des bains. Mais lautre femme hurlait de son ct et disait quelle avait laiss un panier au greffe. Elle numrait tout ce quelle y avait mis. Il fallait vrifier, car tout cela cotait cher. Mon autre voisin et sa mre se regardaient toujours. Le murmure des Arabes continuait au-dessous de nous. Dehors la lumire a sembl se gonfler contre la baie.

Je me sentais un peu malade et jaurais voulu partir. Le bruit me faisait mal. Mais dun autre ct, je voulais profiter encore de la prsence de Marie. Je ne sais pas combien de temps a pass. Marie ma parl de son travail et elle souriait sans arrt. Le murmure, les cris, les conversations se croisaient. Le seul lot de silence tait  ct de moi dans ce petit jeune homme et cette vieille qui se regardaient. Peu  peu, on a emmen les Arabes. Presque tout le monde sest tu ds que le premier est sorti. La petite vieille sest rapproche des barreaux et, au mme moment, un gardien a fait signe  son fils. Il a dit :  Au revoir, maman  et elle a pass sa main entre deux barreaux pour lui faire un petit signe lent et prolong.

Elle est partie pendant quun homme entrait, le chapeau  la main, et prenait sa place. On a introduit un prisonnier et ils se sont parl avec animation, mais  demi-voix, parce que la pice tait redevenue silencieuse. On est venu chercher mon voisin de droite et sa femme lui a dit sans baisser le ton comme si elle navait pas remarqu quil ntait plus ncessaire de crier :  Soigne-toi bien et fais attention.  Puis est venu mon tour. Marie a fait signe quelle membrassait. Je me suis retourn avant de disparatre. Elle tait immobile, le visage cras contre la grille, avec le mme sourire cartel et crisp.

Cest peu aprs quelle ma crit. Et cest  partir de ce moment quont commenc les choses dont je nai jamais aim parler. De toute faon, il ne faut rien exagrer et cela ma t plus facile qu dautres. Au dbut de ma dtention, pourtant, ce qui a t le plus dur, cest que javais des penses dhomme libre. Par exemple, lenvie me prenait dtre sur une plage et de descendre vers la mer.  imaginer le bruit des premires vagues sous la plante de mes pieds, lentre du corps dans leau et la dlivrance que jy trouvais, je sentais tout dun coup combien les murs de ma prison taient rapprochs. Mais cela dura quelques mois. Ensuite, je navais que des penses de prisonnier. Jattendais la promenade quotidienne que je faisais dans la cour ou la visite de mon avocat. Je marrangeais trs bien avec le reste de mon temps. Jai souvent pens alors que si lon mavait fait vivre dans un tronc darbre sec, sans autre occupation que de regarder la fleur du ciel au-dessus de ma tte, je my serais peu  peu habitu. Jaurais attendu des passages doiseaux ou des rencontres de nuages comme jattendais ici les curieuses cravates de mon avocat et comme, dans un autre monde, je patientais jusquau samedi pour treindre le corps de Marie. Or,  bien rflchir, je ntais pas dans un arbre sec. Il y avait plus malheureux que moi. Ctait dailleurs une ide de maman, et elle le rptait souvent, quon finissait par shabituer  tout.

Du reste, je nallais pas si loin dordinaire. Les premiers mois ont t durs. Mais justement leffort que jai d faire aidait  les passer. Par exemple, jtais tourment par le dsir dune femme. Ctait naturel, jtais jeune. Je ne pensais jamais  Marie particulirement. Mais je pensais tellement  une femme, aux femmes,  toutes celles que javais connues,  toutes les circonstances o je les avais aimes, que ma cellule semplissait de tous les visages et se peuplait de mes dsirs. Dans un sens, cela me dsquilibrait. Mais dans un autre, cela tuait le temps. Javais fini par gagner la sympathie du gardien-chef qui accompagnait  lheure des repas le garon de cuisine. Cest lui qui, dabord, ma parl des femmes. Il ma dit que ctait la premire chose dont se plaignaient les autres. Je lui ai dit que jtais comme eux et que je trouvais ce traitement injuste.  Mais, a-t-il dit, cest justement pour a quon vous met en prison.  Comment, pour a ?  Mais oui, la libert, cest a. On vous prive de la libert.  Je navais jamais pens  cela. Je lai approuv :  Cest vrai, lui ai-je dit, o serait la punition ?  Oui, vous comprenez les choses, vous. Les autres non. Mais ils finissent par se soulager eux-mmes.  Le gardien est parti ensuite.

Il y a eu aussi les cigarettes. Quand je suis entr en prison, on ma pris ma ceinture, mes cordons de souliers, ma cravate et tout ce que je portais dans mes poches, mes cigarettes en particulier. Une fois en cellule, jai demand quon me les rende. Mais on ma dit que ctait dfendu. Les premiers jours ont t trs durs. Cest peut-tre cela qui ma le plus abattu. Je suais des morceaux de bois que jarrachais de la planche de mon lit. Je promenais toute la journe une nause perptuelle. Je ne comprenais pas pourquoi on me privait de cela qui ne faisait de mal  personne. Plus tard, jai compris que cela faisait partie aussi de la punition. Mais  ce moment-l, je mtais habitu  ne plus fumer et cette punition nen tait plus une pour moi.

 part ces ennuis, je ntais pas trop malheureux. Toute la question, encore une fois, tait de tuer le temps. Jai fini par ne plus mennuyer du tout  partir de linstant o jai appris  me souvenir. Je me mettais quelquefois  penser  ma chambre et, en imagination, je partais dun coin pour y revenir en dnombrant mentalement tout ce qui se trouvait sur mon chemin. Au dbut, ctait vite fait. Mais chaque fois que je recommenais, ctait un peu plus long. Car je me souvenais de chaque meuble, et, pour chacun dentre eux, de chaque objet qui sy trouvait et, pour chaque objet, de tous les dtails et pour les dtails eux-mmes, une incrustation, une flure ou un bord brch, de leur couleur ou de leur grain. En mme temps, jessayais de ne pas perdre le fil de mon inventaire, de faire une numration complte. Si bien quau bout de quelques semaines, je pouvais passer des heures, rien qu dnombrer ce qui se trouvait dans ma chambre. Ainsi, plus je rflchissais et plus de choses mconnues et oublies je sortais de ma mmoire. Jai compris alors quun homme qui naurait vcu quun seul jour pourrait sans peine vivre cent ans dans une prison. Il aurait assez de souvenirs pour ne pas sennuyer. Dans un sens, ctait un avantage.

Il y avait aussi le sommeil. Au dbut, je dormais mal la nuit et pas du tout le jour. Peu  peu, mes nuits ont t meilleures et jai pu dormir aussi le jour. Je peux dire que, dans les derniers mois, je dormais de seize  dix-huit heures par jour. Il me restait alors six heures  tuer avec les repas, les besoins naturels, mes souvenirs et lhistoire du Tchcoslovaque.

Entre ma paillasse et la planche du lit, javais trouv, en effet, un vieux morceau de journal presque coll  ltoffe, jauni et transparent. Il relatait un fait divers dont le dbut manquait, mais qui avait d se passer en Tchcoslovaquie. Un homme tait parti dun village tchque pour faire fortune. Au bout de vingt-cinq ans, riche, il tait revenu avec une femme et un enfant. Sa mre tenait un htel avec sa sur dans son village natal. Pour les surprendre, il avait laiss sa femme et son enfant dans un autre tablissement, tait all chez sa mre qui ne lavait pas reconnu quand il tait entr. Par plaisanterie, il avait eu lide de prendre une chambre. Il avait montr son argent. Dans la nuit, sa mre et sa sur lavaient assassin  coups de marteau pour le voler et avaient jet son corps dans la rivire. Le matin, la femme tait venue, avait rvl sans le savoir lidentit du voyageur. La mre stait pendue. La sur stait jete dans un puits. Jai d lire cette histoire des milliers de fois. Dun ct, elle tait invraisemblable. Dun autre, elle tait naturelle. De toute faon, je trouvais que le voyageur lavait un peu mrit et quil ne faut jamais jouer.

Ainsi, avec les heures de sommeil, les souvenirs, la lecture de mon fait divers et lalternance de la lumire et de lombre, le temps a pass. Javais bien lu quon finissait par perdre la notion du temps en prison. Mais cela navait pas beaucoup de sens pour moi. Je navais pas compris  quel point les jours pouvaient tre  la fois longs et courts. Longs  vivre sans doute, mais tellement distendus quils finissaient par dborder les uns sur les autres. Ils y perdaient leur nom. Les mots hier ou demain taient les seuls qui gardaient un sens pour moi.

Lorsquun jour, le gardien ma dit que jtais l depuis cinq mois, je lai cru, mais je ne lai pas compris. Pour moi, ctait sans cesse le mme jour qui dferlait dans ma cellule et la mme tche que je poursuivais. Ce jour-l, aprs le dpart du gardien, je me suis regard dans ma gamelle de fer. Il ma sembl que mon image restait srieuse alors mme que jessayais de lui sourire. Je lai agite devant moi. Jai souri et elle a gard le mme air svre et triste. Le jour finissait et ctait lheure dont je ne veux pas parler, lheure sans nom, o les bruits du soir montaient de tous les tages de la prison dans un cortge de silence. Je me suis approch de la lucarne et, dans la dernire lumire, jai contempl une fois de plus mon image. Elle tait toujours srieuse, et quoi dtonnant puisque,  ce moment, je ltais aussi ? Mais en mme temps et pour la premire fois depuis des mois, jai entendu distinctement le son de ma voix. Je lai reconnue pour celle qui rsonnait dj depuis de longs jours  mes oreilles et jai compris que pendant tout ce temps javais parl seul. Je me suis souvenu alors de ce que disait linfirmire  lenterrement de maman. Non, il ny avait pas dissue et personne ne peut imaginer ce que sont les soirs dans les prisons.

III
Je peux dire quau fond lt a trs vite remplac lt. Je savais quavec la monte des premires chaleurs surviendrait quelque chose de nouveau pour moi. Mon affaire tait inscrite  la dernire session de la cour dassises et cette session se terminerait avec le mois de juin. Les dbats se sont ouverts avec, au-dehors, tout le plein du soleil. Mon avocat mavait assur quils ne dureraient pas plus de deux ou trois jours.  Dailleurs, avait-il ajout, la cour sera presse parce que votre affaire nest pas la plus importante de la session. Il y a un parricide qui passera tout de suite aprs. 

 sept heures et demie du matin, on est venu me chercher et la voiture cellulaire ma conduit au Palais de justice. Les deux gendarmes mont fait entrer dans une petite pice qui sentait lombre. Nous avons attendu, assis prs dune porte derrire laquelle on entendait des voix, des appels, des bruits de chaises et tout un remue-mnage qui ma fait penser  ces ftes de quartier o, aprs le concert, on range la salle pour pouvoir danser. Les gendarmes mont dit quil fallait attendre la cour et lun deux ma offert une cigarette que jai refuse. Il ma demand peu aprs  si javais le trac . Jai rpondu que non. Et mme, dans un sens, cela mintressait de voir un procs. Je nen avais jamais eu loccasion dans ma vie :  Oui, a dit le second gendarme, mais cela finit par fatiguer. 

Aprs un peu de temps, une petite sonnerie a rsonn dans la pice. Ils mont alors t les menottes. Ils ont ouvert la porte et mont fait entrer dans le box des accuss. La salle tait pleine  craquer. Malgr les stores, le soleil sinfiltrait par endroits et lair tait dj touffant. On avait laiss les vitres closes. Je me suis assis et les gendarmes mont encadr. Cest  ce moment que jai aperu une range de visages devant moi. Tous me regardaient : jai compris que ctaient les jurs. Mais je ne peux pas dire ce qui les distinguait les uns des autres. Je nai eu quune impression : jtais devant une banquette de tramway et tous ces voyageurs anonymes piaient le nouvel arrivant pour en apercevoir les ridicules. Je sais bien que ctait une ide niaise puisque ici ce ntait pas le ridicule quils cherchaient, mais le crime. Cependant la diffrence nest pas grande et cest en tout cas lide qui mest venue.

Jtais un peu tourdi aussi par tout ce monde dans cette salle close. Jai regard encore le prtoire et je nai distingu aucun visage. Je crois bien que dabord je ne mtais pas rendu compte que tout ce monde se pressait pour me voir. Dhabitude, les gens ne soccupaient pas de ma personne. Il ma fallu un effort pour comprendre que jtais la cause de toute cette agitation. Jai dit au gendarme :  Que de monde !  Il ma rpondu que ctait  cause des journaux et il ma montr un groupe qui se tenait prs dune table sous le banc des jurs. Il ma dit :  Les voil.  Jai demand :  Qui ?  et il a rpt :  Les journaux.  Il connaissait lun des journalistes qui la vu  ce moment et qui sest dirig vers nous. Ctait un homme dj g, sympathique, avec un visage un peu grimaant. Il a serr la main du gendarme avec beaucoup de chaleur. Jai remarqu  ce moment que tout le monde se rencontrait, sinterpellait et conversait, comme dans un club o lon est heureux de se retrouver entre gens du mme monde. Je me suis expliqu aussi la bizarre impression que javais dtre de trop, un peu comme un intrus. Pourtant, le journaliste sest adress  moi en souriant. Il ma dit quil esprait que tout irait bien pour moi. Je lai remerci et il a ajout :  Vous savez, nous avons mont un peu votre affaire. Lt, cest la saison creuse pour les journaux. Et il ny avait que votre histoire et celle du parricide qui vaillent quelque chose.  Il ma montr ensuite, dans le groupe quil venait de quitter, un petit bonhomme qui ressemblait  une belette engraisse, avec dnormes lunettes cercles de noir. Il ma dit que ctait lenvoy spcial dun journal de Paris :  Il nest pas venu pour vous, dailleurs. Mais comme il est charg de rendre compte du procs du parricide, on lui a demand de cbler votre affaire en mme temps.  L encore, jai failli le remercier. Mais jai pens que ce serait ridicule. Il ma fait un petit signe cordial de la main et nous a quitts. Nous avons encore attendu quelques minutes.

Mon avocat est arriv, en robe, entour de beaucoup dautres confrres. Il est all vers les journalistes, a serr des mains. Ils ont plaisant, ri et avaient lair tout  fait  leur aise, jusquau moment o la sonnerie a retenti dans le prtoire. Tout le monde a regagn sa place. Mon avocat est venu vers moi, ma serr la main et ma conseill de rpondre brivement aux questions quon me poserait, de ne pas prendre dinitiatives et de me reposer sur lui pour le reste.

 ma gauche, jai entendu le bruit dune chaise quon reculait et jai vu un grand homme mince, vtu de rouge, portant lorgnon, qui sasseyait en pliant sa robe avec soin. Ctait le procureur. Un huissier a annonc la cour. Au mme moment, deux gros ventilateurs ont commenc de vrombir. Trois juges, deux en noir, le troisime en rouge, sont entrs avec des dossiers et ont march trs vite vers la tribune qui dominait la salle. Lhomme en robe rouge sest assis sur le fauteuil du milieu, a pos sa toque devant lui, essuy son petit crne chauve avec un mouchoir et dclar que laudience tait ouverte.

Les journalistes tenaient dj leur stylo en main. Ils avaient tous le mme air indiffrent et un peu narquois. Pourtant, lun dentre eux, beaucoup plus jeune, habill en flanelle grise avec une cravate bleue, avait laiss son stylo devant lui et me regardait. Dans son visage un peu asymtrique, je ne voyais que ses deux yeux, trs clairs, qui mexaminaient attentivement, sans rien exprimer qui ft dfinissable. Et jai eu limpression bizarre dtre regard par moi-mme. Cest peut-tre pour cela, et aussi parce que je ne connaissais pas les usages du lieu, que je nai pas trs bien compris tout ce qui sest pass ensuite, le tirage au sort des jurs, les questions poses par le prsident  lavocat, au procureur et au jury ( chaque fois, toutes les ttes des jurs se retournaient en mme temps vers la cour), une lecture rapide de lacte daccusation, o je reconnaissais des noms de lieux et de personnes, et de nouvelles questions  mon avocat.

Mais le prsident a dit quil allait faire procder  lappel des tmoins. Lhuissier a lu des noms qui ont attir mon attention. Du sein de ce public tout  lheure informe, jai vu se lever un  un, pour disparatre ensuite par une porte latrale, le directeur et le concierge de lasile, le vieux Thomas Prez, Raymond, Masson, Salamano, Marie. Celle-ci ma fait un petit signe anxieux. Je mtonnais encore de ne pas les avoir aperus plus tt, lorsque  lappel de son nom, le dernier, Cleste sest lev. Jai reconnu  ct de lui la petite bonne femme du restaurant, avec sa jaquette et son air prcis et dcid. Elle me regardait avec intensit. Mais je nai pas eu le temps de rflchir parce que le prsident a pris la parole. Il a dit que les vritables dbats allaient commencer et quil croyait inutile de recommander au public dtre calme. Selon lui, il tait l pour diriger avec impartialit les dbats dune affaire quil voulait considrer avec objectivit. La sentence rendue par le jury serait prise dans un esprit de justice et, dans tous les cas, il ferait vacuer la salle au moindre incident.

La chaleur montait et je voyais dans la salle les assistants sventer avec des journaux. Cela faisait un petit bruit continu de papier froiss. Le prsident a fait un signe et lhuissier a apport trois ventails de paille tresse que les trois juges ont utiliss immdiatement.

Mon interrogatoire a commenc aussitt. Le prsident ma questionn avec calme et mme, ma-t-il sembl, avec une nuance de cordialit. On ma encore fait dcliner mon identit et malgr mon agacement, jai pens quau fond ctait assez naturel, parce quil serait trop grave de juger un homme pour un autre. Puis le prsident a recommenc le rcit de ce que javais fait, en sadressant  moi toutes les trois phrases pour me demander :  Est-ce bien cela ?   chaque fois, jai rpondu :  Oui, monsieur le Prsident , selon les instructions de mon avocat. Cela a t long parce que le prsident apportait beaucoup de minutie dans son rcit. Pendant tout ce temps, les journalistes crivaient. Je sentais les regards du plus jeune dentre eux et de la petite automate. La banquette de tramway tait tout entire tourne vers le prsident. Celui-ci a touss, feuillet son dossier et il sest tourn vers moi en sventant.

Il ma dit quil devait aborder maintenant des questions apparemment trangres  mon affaire, mais qui peut-tre la touchaient de fort prs. Jai compris quil allait encore parler de maman et jai senti en mme temps combien cela mennuyait. Il ma demand pourquoi javais mis maman  lasile. Jai rpondu que ctait parce que je manquais dargent pour la faire garder et soigner. Il ma demand si cela mavait cot personnellement et jai rpondu que ni maman ni moi nattendions plus rien lun de lautre, ni dailleurs de personne, et que nous nous tions habitus tous les deux  nos vies nouvelles. Le prsident a dit alors quil ne voulait pas insister sur ce point et il a demand au procureur sil ne voyait pas dautre question  me poser.

Celui-ci me tournait  demi le dos et, sans me regarder, il a dclar quavec lautorisation du prsident, il aimerait savoir si jtais retourn vers la source tout seul avec lintention de tuer lArabe.  Non , ai-je dit.  Alors, pourquoi tait-il arm et pourquoi revenir vers cet endroit prcisment ?  Jai dit que ctait le hasard. Et le procureur a not avec un accent mauvais :  Ce sera tout pour le moment.  Tout ensuite a t un peu confus, du moins pour moi. Mais aprs quelques conciliabules, le prsident a dclar que laudience tait leve et renvoye  laprs-midi pour laudition des tmoins.

Je nai pas eu le temps de rflchir. On ma emmen, fait monter dans la voiture cellulaire et conduit  la prison o jai mang. Au bout de trs peu de temps, juste assez pour me rendre compte que jtais fatigu, on est revenu me chercher ; tout a recommenc et je me suis trouv dans la mme salle, devant les mmes visages. Seulement la chaleur tait beaucoup plus forte et comme par un miracle chacun des jurs, le procureur, mon avocat et quelques journalistes taient munis aussi dventails de paille. Le jeune journaliste et la petite femme taient toujours l. Mais ils ne sventaient pas et me regardaient encore sans rien dire.

Jai essuy la sueur qui couvrait mon visage et je nai repris un peu conscience du lieu et de moi-mme que lorsque jai entendu appeler le directeur de lasile. On lui a demand si maman se plaignait de moi et il a dit que oui mais que ctait un peu la manie de ses pensionnaires de se plaindre de leurs proches. Le prsident lui a fait prciser si elle me reprochait de lavoir mise  lasile et le directeur a dit encore oui. Mais cette fois, il na rien ajout.  une autre question, il a rpondu quil avait t surpris de mon calme le jour de lenterrement. On lui a demand ce quil entendait par calme. Le directeur a regard alors le bout de ses souliers et il a dit que je navais pas voulu voir maman, je navais pas pleur une seule fois et jtais parti aussitt aprs lenterrement sans me recueillir sur sa tombe. Une chose encore lavait surpris : un employ des pompes funbres lui avait dit que je ne savais pas lge de maman. Il y a eu un moment de silence et le prsident lui a demand si ctait bien de moi quil avait parl. Comme le directeur ne comprenait pas la question, il lui a dit :  Cest la loi.  Puis le prsident a demand  lavocat gnral sil navait pas de question  poser au tmoin et le procureur sest cri :  Oh ! non, cela suffit , avec un tel clat et un tel regard triomphant dans ma direction que, pour la premire fois depuis bien des annes, jai eu une envie stupide de pleurer parce que jai senti combien jtais dtest par tous ces gens-l.

Aprs avoir demand au jury et  mon avocat sils avaient des questions  poser, le prsident a entendu le concierge. Pour lui comme pour tous les autres, le mme crmonial sest rpt. En arrivant, le concierge ma regard et il a dtourn les yeux. Il a rpondu aux questions quon lui posait. Il a dit que je navais pas voulu voir maman, que javais fum, que javais dormi et que javais pris du caf au lait. Jai senti alors quelque chose qui soulevait toute la salle et, pour la premire fois, jai compris que jtais coupable. On a fait rpter au concierge lhistoire du caf au lait et celle de la cigarette. Lavocat gnral ma regard avec une lueur ironique dans les yeux.  ce moment, mon avocat a demand au concierge sil navait pas fum avec moi. Mais le procureur sest lev avec violence contre cette question :  Quel est le criminel ici et quelles sont ces mthodes qui consistent  salir les tmoins de laccusation pour minimiser des tmoignages qui nen demeurent pas moins crasants !  Malgr tout, le prsident a demand au concierge de rpondre  la question. Le vieux a dit dun air embarrass :  Je sais bien que jai eu tort. Mais je nai pas os refuser la cigarette que Monsieur ma offerte.  En dernier lieu, on ma demand si je navais rien  ajouter.  Rien, ai-je rpondu, seulement que le tmoin a raison. Il est vrai que je lui ai offert une cigarette.  Le concierge ma regard alors avec un peu dtonnement et une sorte de gratitude. Il a hsit, puis il a dit que ctait lui qui mavait offert le caf au lait. Mon avocat a triomph bruyamment et a dclar que les jurs apprcieraient. Mais le procureur a tonn au-dessus de nos ttes et il a dit :  Oui, MM. les Jurs apprcieront. Et ils concluront quun tranger pouvait proposer du caf, mais quun fils devait le refuser devant le corps de celle qui lui avait donn le jour.  Le concierge a regagn son banc.

Quand est venu le tour de Thomas Prez, un huissier a d le soutenir jusqu la barre. Prez a dit quil avait surtout connu ma mre et quil ne mavait vu quune fois, le jour de lenterrement. On lui a demand ce que javais fait ce jour-l et il a rpondu :  Vous comprenez, moi-mme javais trop de peine. Alors, je nai rien vu. Ctait la peine qui mempchait de voir. Parce que ctait pour moi une trs grosse peine. Et mme, je me suis vanoui. Alors, je nai pas pu voir monsieur.  Lavocat gnral lui a demand si, du moins, il mavait vu pleurer. Prez a rpondu que non. Le procureur a dit alors  son tour :  MM. les Jurs apprcieront.  Mais mon avocat sest fch. Il a demand  Prez, sur un ton qui ma sembl exagr,  sil avait vu que je ne pleurais pas . Prez a dit :  Non.  Le public a ri. Et mon avocat, en retroussant une de ses manches, a dit dun ton premptoire :  Voil limage de ce procs. Tout est vrai et rien nest vrai !  Le procureur avait le visage ferm et piquait un crayon dans les titres de ses dossiers.

Aprs cinq minutes de suspension pendant lesquelles mon avocat ma dit que tout allait pour le mieux, on a entendu Cleste qui tait cit par la dfense. La dfense, ctait moi. Cleste jetait de temps en temps des regards de mon ct et roulait un panama entre ses mains. Il portait le costume neuf quil mettait pour venir avec moi, certains dimanches, aux courses de chevaux. Mais je crois quil navait pas pu mettre son col parce quil portait seulement un bouton de cuivre pour tenir sa chemise ferme. On lui a demand si jtais son client et il a dit :  Oui, mais ctait aussi un ami  ; ce quil pensait de moi et il a rpondu que jtais un homme ; ce quil entendait par l et il a dclar que tout le monde savait ce que cela voulait dire ; sil avait remarqu que jtais renferm et il a reconnu seulement que je ne parlais pas pour ne rien dire. Lavocat gnral lui a demand si je payais rgulirement ma pension. Cleste a ri et il a dclar :  Ctaient des dtails entre nous.  On lui a demand encore ce quil pensait de mon crime. Il a mis alors ses mains sur la barre et lon voyait quil avait prpar quelque chose. Il a dit :  Pour moi, cest un malheur. Un malheur, tout le monde sait ce que cest. a vous laisse sans dfense. Eh bien ! pour moi cest un malheur.  Il allait continuer, mais le prsident lui a dit que ctait bien et quon le remerciait. Alors Cleste est rest un peu interdit. Mais il a dclar quil voulait encore parler. On lui a demand dtre bref. Il a encore rpt que ctait un malheur. Et le prsident lui a dit :  Oui, cest entendu. Mais nous sommes l pour juger les malheurs de ce genre. Nous vous remercions.  Comme sil tait arriv au bout de sa science et de sa bonne volont, Cleste sest alors retourn vers moi. Il ma sembl que ses yeux brillaient et que ses lvres tremblaient. Il avait lair de me demander ce quil pouvait encore faire. Moi, je nai rien dit, je nai fait aucun geste, mais cest la premire fois de ma vie que jai eu envie dembrasser un homme. Le prsident lui a encore enjoint de quitter la barre. Cleste est all sasseoir dans le prtoire. Pendant tout le reste de laudience, il est rest l, un peu pench en avant, les coudes sur les genoux, le panama entre les mains,  couter tout ce qui se disait. Marie est entre. Elle avait mis un chapeau et elle tait encore belle. Mais je laimais mieux avec ses cheveux libres. De lendroit o jtais, je devinais le poids lger de ses seins et je reconnaissais sa lvre infrieure toujours un peu gonfle. Elle semblait trs nerveuse. Tout de suite, on lui a demand depuis quand elle me connaissait. Elle a indiqu lpoque o elle travaillait chez nous. Le prsident a voulu savoir quels taient ses rapports avec moi. Elle a dit quelle tait mon amie.  une autre question, elle a rpondu quil tait vrai quelle devait mpouser. Le procureur qui feuilletait un dossier lui a demand brusquement de quand datait notre liaison. Elle a indiqu la date. Le procureur a remarqu dun air indiffrent quil lui semblait que ctait le lendemain de la mort de maman. Puis il a dit avec quelque ironie quil ne voudrait pas insister sur une situation dlicate, quil comprenait bien les scrupules de Marie, mais (et ici son accent sest fait plus dur) que son devoir lui commandait de slever au-dessus des convenances. Il a donc demand  Marie de rsumer cette journe o je lavais connue. Marie ne voulait pas parler, mais devant linsistance du procureur, elle a dit notre bain, notre sortie au cinma et notre rentre chez moi. Lavocat gnral a dit qu la suite des dclarations de Marie  linstruction, il avait consult les programmes de cette date. Il a ajout que Marie elle-mme dirait quel film on passait alors. Dune voix presque blanche, en effet, elle a indiqu que ctait un film de Fernandel. Le silence tait complet dans la salle quand elle a eu fini. Le procureur sest alors lev, trs grave et dune voix que jai trouve vraiment mue, le doigt tendu vers moi, il a articul lentement :  Messieurs les Jurs, le lendemain de la mort de sa mre, cet homme prenait des bains, commenait une liaison irrgulire, et allait rire devant un film comique. Je nai rien de plus  vous dire.  Il sest assis, toujours dans le silence. Mais, tout dun coup, Marie a clat en sanglots, a dit que ce ntait pas cela, quil y avait autre chose, quon la forait  dire le contraire de ce quelle pensait, quelle me connaissait bien et que je navais rien fait de mal. Mais lhuissier, sur un signe du prsident, la emmene et laudience sest poursuivie.

Cest  peine si, ensuite, on a cout Masson qui a dclar que jtais un honnte homme  et quil dirait plus, jtais un brave homme . Cest  peine encore si on a cout Salamano quand il a rappel que javais t bon pour son chien et quand il a rpondu  une question sur ma mre et sur moi en disant que je navais plus rien  dire  maman et que je lavais mise pour cette raison  lasile.  Il faut comprendre, disait Salamano, il faut comprendre.  Mais personne ne paraissait comprendre. On la emmen.

Puis est venu le tour de Raymond, qui tait le dernier tmoin. Raymond ma fait un petit signe et a dit tout de suite que jtais innocent. Mais le prsident a dclar quon ne lui demandait pas des apprciations, mais des faits. Il la invit  attendre des questions pour rpondre. On lui a fait prciser ses relations avec la victime. Raymond en a profit pour dire que ctait lui que cette dernire hassait depuis quil avait gifl sa sur. Le prsident lui a demand cependant si la victime navait pas de raison de me har. Raymond a dit que ma prsence  la plage tait le rsultat dun hasard. Le procureur lui a demand alors comment il se faisait que la lettre qui tait  lorigine du drame avait t crite par moi. Raymond a rpondu que ctait un hasard. Le procureur a rtorqu que le hasard avait dj beaucoup de mfaits sur la conscience dans cette histoire. Il a voulu savoir si ctait par hasard que je ntais pas intervenu quand Raymond avait gifl sa matresse, par hasard que javais servi de tmoin au commissariat, par hasard encore que mes dclarations lors de ce tmoignage staient rvles de pure complaisance. Pour finir, il a demand  Raymond quels taient ses moyens dexistence, et comme ce dernier rpondait :  Magasinier , lavocat gnral a dclar aux jurs que de notorit gnrale le tmoin exerait le mtier de souteneur. Jtais son complice et son ami. Il sagissait dun drame crapuleux de la plus basse espce, aggrav du fait quon avait affaire  un monstre moral. Raymond a voulu se dfendre et mon avocat a protest, mais on leur a dit quil fallait laisser terminer le procureur. Celui-ci a dit :  Jai peu de chose  ajouter. tait-il votre ami ?  a-t-il demand  Raymond.  Oui, a dit celui-ci, ctait mon copain.  Lavocat gnral ma pos alors la mme question et jai regard Raymond qui na pas dtourn les yeux. Jai rpondu :  Oui.  Le procureur sest alors retourn vers le jury et a dclar :  Le mme homme qui au lendemain de la mort de sa mre se livrait  la dbauche la plus honteuse a tu pour des raisons futiles et pour liquider une affaire de murs inqualifiable. 

Il sest assis alors. Mais mon avocat,  bout de patience, sest cri en levant les bras, de sorte que ses manches en retombant ont dcouvert les plis dune chemise amidonne :  Enfin, est-il accus davoir enterr sa mre ou davoir tu un homme ?  Le public a ri. Mais le procureur sest redress encore, sest drap dans sa robe et a dclar quil fallait avoir lingnuit de lhonorable dfenseur pour ne pas sentir quil y avait entre ces deux ordres de faits une relation profonde, pathtique, essentielle.  Oui, sest-il cri avec force, jaccuse cet homme davoir enterr une mre avec un cur de criminel.  Cette dclaration a paru faire un effet considrable sur le public. Mon avocat a hauss les paules et essuy la sueur qui couvrait son front. Mais lui-mme paraissait branl et jai compris que les choses nallaient pas bien pour moi.

Laudience a t leve. En sortant du palais de justice pour monter dans la voiture, jai reconnu un court instant lodeur et la couleur du soir dt. Dans lobscurit de ma prison roulante, jai retrouv un  un, comme du fond de ma fatigue, tous les bruits familiers dune ville que jaimais et dune certaine heure o il marrivait de me sentir content. Le cri des vendeurs de journaux dans lair dj dtendu, les derniers oiseaux dans le square, lappel des marchands de sandwiches, la plainte des tramways dans les hauts tournants de la ville et cette rumeur du ciel avant que la nuit bascule sur le port, tout cela recomposait pour moi un itinraire daveugle, que je connaissais bien avant dentrer en prison. Oui, ctait lheure o, il y avait bien longtemps, je me sentais content. Ce qui mattendait alors, ctait toujours un sommeil lger et sans rves. Et pourtant quelque chose tait chang puisque, avec lattente du lendemain, cest ma cellule que jai retrouve. Comme si les chemins familiers tracs dans les ciels dt pouvaient mener aussi bien aux prisons quaux sommeils innocents.

IV
Mme sur un banc daccus, il est toujours intressant dentendre parler de soi. Pendant les plaidoiries du procureur et de mon avocat, je peux dire quon a beaucoup parl de moi et peut-tre plus de moi que de mon crime. taient-elles si diffrentes, dailleurs, ces plaidoiries ? Lavocat levait les bras et plaidait coupable, mais avec excuses. Le procureur tendait ses mains et dnonait la culpabilit, mais sans excuses. Une chose pourtant me gnait vaguement. Malgr mes proccupations, jtais parfois tent dintervenir et mon avocat me disait alors :  Taisez-vous, cela vaut mieux pour votre affaire.  En quelque sorte, on avait lair de traiter cette affaire en dehors de moi. Tout se droulait sans mon intervention. Mon sort se rglait sans quon prenne mon avis. De temps en temps, javais envie dinterrompre tout le monde et de dire :  Mais tout de mme, qui est laccus ? Cest important dtre laccus. Et jai quelque chose  dire.  Mais rflexion faite, je navais rien  dire. Dailleurs, je dois reconnatre que lintrt quon trouve  occuper les gens ne dure pas longtemps. Par exemple, la plaidoirie du procureur ma trs vite lass. Ce sont seulement des fragments, des gestes ou des tirades entires, mais dtaches de lensemble, qui mont frapp ou ont veill mon intrt.

Le fond de sa pense, si jai bien compris, cest que javais prmdit mon crime. Du moins, il a essay de le dmontrer. Comme il le disait lui-mme :  Jen ferai la preuve, messieurs, et je la ferai doublement. Sous laveuglante clart des faits dabord et ensuite dans lclairage sombre que me fournira la psychologie de cette me criminelle.  Il a rsum les faits  partir de la mort de maman. Il a rappel mon insensibilit, lignorance o jtais de lge de maman, mon bain du lendemain, avec une femme, le cinma, Fernandel et enfin la rentre avec Marie. Jai mis du temps  le comprendre,  ce moment, parce quil disait  sa matresse  et pour moi, elle tait Marie. Ensuite, il en est venu  lhistoire de Raymond. Jai trouv que sa faon de voir les vnements ne manquait pas de clart. Ce quil disait tait plausible. Javais crit la lettre daccord avec Raymond pour attirer sa matresse et la livrer aux mauvais traitements dun homme  de moralit douteuse . Javais provoqu sur la plage les adversaires de Raymond. Celui-ci avait t bless. Je lui avais demand son revolver. Jtais revenu seul pour men servir. Javais abattu lArabe comme je le projetais. Javais attendu. Et  pour tre sr que la besogne tait bien faite , javais tir encore quatre balles, posment,  coup sr, dune faon rflchie en quelque sorte.

 Et voil, messieurs, a dit lavocat gnral. Jai retrac devant vous le fil dvnements qui a conduit cet homme  tuer en pleine connaissance de cause. Jinsiste l-dessus, a-t-il dit. Car il ne sagit pas dun assassinat ordinaire, dun acte irrflchi que vous pourriez estimer attnu par les circonstances. Cet homme, messieurs, cet homme est intelligent. Vous lavez entendu, nest-ce pas ? Il sait rpondre. Il connat la valeur des mots. Et lon ne peut pas dire quil a agi sans se rendre compte de ce quil faisait. 

Moi jcoutais et jentendais quon me jugeait intelligent. Mais je ne comprenais pas bien comment les qualits dun homme ordinaire pouvaient devenir des charges crasantes contre un coupable. Du moins, ctait cela qui me frappait et je nai plus cout le procureur jusquau moment o je lai entendu dire :  A-t-il seulement exprim des regrets ? Jamais, messieurs. Pas une seule fois au cours de linstruction cet homme na paru mu de son abominable forfait.   ce moment, il sest tourn vers moi et ma dsign du doigt en continuant  maccabler sans quen ralit je comprenne bien pourquoi. Sans doute, je ne pouvais pas mempcher de reconnatre quil avait raison. Je ne regrettais pas beaucoup mon acte. Mais tant dacharnement mtonnait. Jaurais voulu essayer de lui expliquer cordialement, presque avec affection, que je navais jamais pu regretter vraiment quelque chose. Jtais toujours pris par ce qui allait arriver, par aujourdhui ou par demain. Mais naturellement, dans ltat o lon mavait mis, je ne pouvais parler  personne sur ce ton. Je navais pas le droit de me montrer affectueux, davoir de la bonne volont. Et jai essay dcouter encore parce que le procureur sest mis  parler de mon me.

Il disait quil stait pench sur elle et quil navait rien trouv, messieurs les Jurs. Il disait qu la vrit, je nen avais point, dme, et que rien dhumain, et pas un des principes moraux qui gardent le cur des hommes ne mtait accessible.  Sans doute, ajoutait-il, nous ne saurions le lui reprocher. Ce quil ne saurait acqurir, nous ne pouvons nous plaindre quil en manque. Mais quand il sagit de cette cour, la vertu toute ngative de la tolrance doit se muer en celle, moins facile, mais plus leve, de la justice. Surtout lorsque le vide du cur tel quon le dcouvre chez cet homme devient un gouffre o la socit peut succomber.  Cest alors quil a parl de mon attitude envers maman. Il a rpt ce quil avait dit pendant les dbats. Mais il a t beaucoup plus long que lorsquil parlait de mon crime, si long mme que, finalement, je nai plus senti que la chaleur de cette matine. Jusquau moment, du moins, o lavocat gnral sest arrt et, aprs un moment de silence, a repris dune voix trs basse et trs pntre :  Cette mme cour, messieurs, va juger demain le plus abominable des forfaits : le meurtre dun pre.  Selon lui, limagination reculait devant cet atroce attentat. Il osait esprer que la justice des hommes punirait sans faiblesse. Mais, il ne craignait pas de le dire, lhorreur que lui inspirait ce crime le cdait presque  celle quil ressentait devant mon insensibilit. Toujours selon lui, un homme qui tuait moralement sa mre se retranchait de la socit des hommes au mme titre que celui qui portait une main meurtrire sur lauteur de ses jours. Dans tous les cas, le premier prparait les actes du second, il les annonait en quelque sorte et il les lgitimait.  Jen suis persuad, messieurs, a-t-il ajout en levant la voix, vous ne trouverez pas ma pense trop audacieuse, si je dis que lhomme qui est assis sur ce banc est coupable aussi du meurtre que cette cour devra juger demain. Il doit tre puni en consquence.  Ici, le procureur a essuy son visage brillant de sueur. Il a dit enfin que son devoir tait douloureux, mais quil laccomplirait fermement. Il a dclar que je navais rien  faire avec une socit dont je mconnaissais les rgles les plus essentielles et que je ne pouvais pas en appeler  ce cur humain dont jignorais les ractions lmentaires.  Je vous demande la tte de cet homme, a-t-il dit, et cest le cur lger que je vous la demande. Car sil mest arriv au cours de ma dj longue carrire de rclamer des peines capitales, jamais autant quaujourdhui, je nai senti ce pnible devoir compens, balanc, clair par la conscience dun commandement imprieux et sacr et par lhorreur que je ressens devant un visage dhomme o je ne lis rien que de monstrueux. 

Quand le procureur sest rassis, il y a eu un moment de silence assez long. Moi, jtais tourdi de chaleur et dtonnement. Le prsident a touss un peu et sur un ton trs bas, il ma demand si je navais rien  ajouter. Je me suis lev et comme javais envie de parler, jai dit, un peu au hasard dailleurs, que je navais pas eu lintention de tuer lArabe. Le prsident a rpondu que ctait une affirmation, que jusquici il saisissait mal mon systme de dfense et quil serait heureux, avant dentendre mon avocat, de me faire prciser les motifs qui avaient inspir mon acte. Jai dit rapidement, en mlant un peu les mots et en me rendant compte de mon ridicule, que ctait  cause du soleil. Il y a eu des rires dans la salle. Mon avocat a hauss les paules et tout de suite aprs, on lui a donn la parole. Mais il a dclar quil tait tard, quil en avait pour plusieurs heures et quil demandait le renvoi  laprs-midi. La cour y a consenti.

Laprs-midi, les grands ventilateurs brassaient toujours lair pais de la salle, et les petits ventails multicolores des jurs sagitaient tous dans le mme sens. La plaidoirie de mon avocat me semblait ne devoir jamais finir.  un moment donn, cependant, je lai cout parce quil disait :  Il est vrai que jai tu.  Puis il a continu sur ce ton, disant  je  chaque fois quil parlait de moi. Jtais trs tonn. Je me suis pench vers un gendarme et je lui ai demand pourquoi. Il ma dit de me taire et, aprs un moment, il a ajout :  Tous les avocats font a.  Moi, jai pens que ctait mcarter encore de laffaire, me rduire  zro et, en un certain sens, se substituer  moi. Mais je crois que jtais dj trs loin de cette salle daudience. Dailleurs, mon avocat ma sembl ridicule. Il a plaid la provocation trs rapidement et puis lui aussi a parl de mon me. Mais il ma paru quil avait beaucoup moins de talent que le procureur.  Moi aussi, a-t-il dit, je me suis pench sur cette me, mais, contrairement  lminent reprsentant du ministre public, jai trouv quelque chose et je puis dire que jy ai lu  livre ouvert.  Il y avait lu que jtais un honnte homme, un travailleur rgulier, infatigable, fidle  la maison qui lemployait, aim de tous et compatissant aux misres dautrui. Pour lui, jtais un fils modle qui avait soutenu sa mre aussi longtemps quil lavait pu. Finalement javais espr quune maison de retraite donnerait  la vieille femme le confort que mes moyens ne me permettaient pas de lui procurer.  Je mtonne, messieurs, a-t-il ajout, quon ait men si grand bruit autour de cet asile. Car enfin, sil fallait donner une preuve de lutilit et de la grandeur de ces institutions, il faudrait bien dire que cest ltat lui-mme qui les subventionne.  Seulement, il na pas parl de lenterrement et jai senti que cela manquait dans sa plaidoirie. Mais  cause de toutes ces longues phrases, de toutes ces journes et ces heures interminables pendant lesquelles on avait parl de mon me, jai eu limpression que tout devenait comme une eau incolore o je trouvais le vertige.

 la fin, je me souviens seulement que, de la rue et  travers tout lespace des salles et des prtoires, pendant que mon avocat continuait  parler, la trompette dun marchand de glace a rsonn jusqu moi. Jai t assailli des souvenirs dune vie qui ne mappartenait plus, mais o javais trouv les plus pauvres et les plus tenaces de mes joies : des odeurs dt, le quartier que jaimais, un certain ciel du soir, le rire et les robes de Marie. Tout ce que je faisais dinutile en ce lieu mest alors remont  la gorge et je nai eu quune hte, cest quon en finisse et que je retrouve ma cellule avec le sommeil. Cest  peine si jai entendu mon avocat scrier, pour finir, que les jurs ne voudraient pas envoyer  la mort un travailleur honnte perdu par une minute dgarement, et demander les circonstances attnuantes pour un crime dont je tranais dj, comme le plus sr de mes chtiments, le remords ternel. La cour a suspendu laudience et lavocat sest assis dun air puis. Mais ses collgues sont venus vers lui pour lui serrer la main. Jai entendu :  Magnifique, mon cher.  Lun deux ma mme pris  tmoin :  Hein ?  ma-t-il dit. Jai acquiesc, mais mon compliment ntait pas sincre, parce que jtais trop fatigu.

Pourtant, lheure dclinait au-dehors et la chaleur tait moins forte. Aux quelques bruits de rue que jentendais, je devinais la douceur du soir. Nous tions l, tous,  attendre. Et ce quensemble nous attendions ne concernait que moi. Jai encore regard la salle. Tout tait dans le mme tat que le premier jour. Jai rencontr le regard du journaliste  la veste grise et de la femme automate. Cela ma donn  penser que je navais pas cherch Marie du regard pendant tout le procs. Je ne lavais pas oublie, mais javais trop  faire. Je lai vue entre Cleste et Raymond. Elle ma fait un petit signe comme si elle disait :  Enfin , et jai vu son visage un peu anxieux qui souriait. Mais je sentais mon cur ferm et je nai mme pas pu rpondre  son sourire.

La cour est revenue. Trs vite, on a lu aux jurs une srie de questions. Jai entendu  coupable de meurtre   prmditation   circonstances attnuantes . Les jurs sont sortis et lon ma emmen dans la petite pice o javais dj attendu. Mon avocat est venu me rejoindre : il tait trs volubile et ma parl avec plus de confiance et de cordialit quil ne lavait jamais fait. Il pensait que tout irait bien et que je men tirerais avec quelques annes de prison ou de bagne. Je lui ai demand sil y avait des chances de cassation en cas de jugement dfavorable. Il ma dit que non. Sa tactique avait t de ne pas dposer de conclusions pour ne pas indisposer le jury. Il ma expliqu quon ne cassait pas un jugement, comme cela, pour rien. Cela ma paru vident et je me suis rendu  ses raisons.  considrer froidement la chose, ctait tout  fait naturel. Dans le cas contraire, il y aurait trop de paperasses inutiles.  De toute faon, ma dit mon avocat, il y a le pourvoi. Mais je suis persuad que lissue sera favorable. 

Nous avons attendu trs longtemps, prs de trois quarts dheure, je crois. Au bout de ce temps, une sonnerie a retenti. Mon avocat ma quitt en disant :  Le prsident du jury va lire les rponses. On ne vous fera entrer que pour lnonc du jugement.  Des portes ont claqu. Des gens couraient dans des escaliers dont je ne savais pas sils taient proches ou loigns. Puis jai entendu une voix sourde lire quelque chose dans la salle. Quand la sonnerie a encore retenti, que la porte du box sest ouverte, cest le silence de la salle qui est mont vers moi, le silence, et cette singulire sensation que jai eue lorsque jai constat que le jeune journaliste avait dtourn ses yeux. Je nai pas regard du ct de Marie. Je nen ai pas eu le temps parce que le prsident ma dit dans une forme bizarre que jaurais la tte tranche sur une place publique au nom du peuple franais. Il ma sembl alors reconnatre le sentiment que je lisais sur tous les visages. Je crois bien que ctait de la considration. Les gendarmes taient trs doux avec moi. Lavocat a pos sa main sur mon poignet. Je ne pensais plus  rien. Mais le prsident ma demand si je navais rien  ajouter. Jai rflchi. Jai dit :  Non.  Cest alors quon ma emmen.

V
Pour la troisime fois, jai refus de recevoir laumnier. Je nai rien  lui dire, je nai pas envie de parler, je le verrai bien assez tt. Ce qui mintresse en ce moment, cest dchapper  la mcanique, de savoir si linvitable peut avoir une issue. On ma chang de cellule. De celle-ci, lorsque je suis allong, je vois le ciel et je ne vois que lui. Toutes mes journes se passent  regarder sur son visage le dclin des couleurs qui conduit le jour  la nuit. Couch, je passe les mains sous ma tte et jattends. Je ne sais combien de fois je me suis demand sil y avait des exemples de condamns  mort qui eussent chapp au mcanisme implacable, disparu avant lexcution, rompu les cordons dagents. Je me reprochais alors de navoir pas prt assez dattention aux rcits dexcution. On devrait toujours sintresser  ces questions. On ne sait jamais ce qui peut arriver. Comme tout le monde, javais lu des comptes rendus dans les journaux. Mais il y avait certainement des ouvrages spciaux que je navais jamais eu la curiosit de consulter. L, peut-tre, jaurais trouv des rcits dvasion. Jaurais appris que dans un cas au moins la roue stait arrte, que dans cette prmditation irrsistible, le hasard et la chance, une fois seulement, avaient chang quelque chose. Une fois ! Dans un sens, je crois que cela maurait suffi. Mon cur aurait fait le reste. Les journaux parlaient souvent dune dette qui tait due  la socit. Il fallait, selon eux, la payer. Mais cela ne parle pas  limagination. Ce qui comptait, ctait une possibilit dvasion, un saut hors du rite implacable, une course  la folie qui offrt toutes les chances de lespoir. Naturellement, lespoir, ctait dtre abattu au coin dune rue, en pleine course, et dune balle  la vole. Mais tout bien considr, rien ne me permettait ce luxe, tout me linterdisait, la mcanique me reprenait.

Malgr ma bonne volont, je ne pouvais pas accepter cette certitude insolente. Car enfin, il y avait une disproportion ridicule entre le jugement qui lavait fonde et son droulement imperturbable  partir du moment o ce jugement avait t prononc. Le fait que la sentence avait t lue  vingt heures plutt qu dix-sept, le fait quelle aurait pu tre tout autre, quelle avait t prise par des hommes qui changent de linge, quelle avait t porte au crdit dune notion aussi imprcise que le peuple franais (ou allemand, ou chinois), il me semblait bien que tout cela enlevait beaucoup de srieux  une telle dcision. Pourtant, jtais oblig de reconnatre que ds la seconde o elle avait t prise, ses effets devenaient aussi certains, aussi srieux, que la prsence de ce mur tout le long duquel jcrasais mon corps.

Je me suis souvenu dans ces moments dune histoire que maman me racontait  propos de mon pre. Je ne lavais pas connu. Tout ce que je connaissais de prcis sur cet homme, ctait peut-tre ce que men disait alors maman : il tait all voir excuter un assassin. Il tait malade  lide dy aller. Il lavait fait cependant et au retour il avait vomi une partie de la matine. Mon pre me dgotait un peu alors. Maintenant je comprenais, ctait si naturel. Comment navais-je pas vu que rien ntait plus important quune excution capitale et que, en somme, ctait la seule chose vraiment intressante pour un homme ! Si jamais je sortais de cette prison, jirais voir toutes les excutions capitales. Javais tort, je crois, de penser  cette possibilit. Car  lide de me voir libre par un petit matin derrire un cordon dagents, de lautre ct en quelque sorte,  lide dtre le spectateur qui vient voir et qui pourra vomir aprs, un flot de joie empoisonne me montait au cur. Mais ce ntait pas raisonnable. Javais tort de me laisser aller  ces suppositions parce que, linstant daprs, javais si affreusement froid que je me recroquevillais sous ma couverture. Je claquais des dents sans pouvoir me retenir.

Mais, naturellement, on ne peut pas tre toujours raisonnable. Dautres fois, par exemple, je faisais des projets de loi. Je rformais les pnalits. Javais remarqu que lessentiel tait de donner une chance au condamn. Une seule sur mille, cela suffisait pour arranger bien des choses. Ainsi, il me semblait quon pouvait trouver une combinaison chimique dont labsorption tuerait le patient (je pensais : le patient) neuf fois sur dix. Lui le saurait, ctait la condition. Car en rflchissant bien, en considrant les choses avec calme, je constatais que ce qui tait dfectueux avec le couperet, cest quil ny avait aucune chance, absolument aucune. Une fois pour toutes, en somme, la mort du patient avait t dcide. Ctait une affaire classe, une combinaison bien arrte, un accord entendu et sur lequel il ntait pas question de revenir. Si le coup ratait, par extraordinaire, on recommenait. Par suite, ce quil y avait dennuyeux, cest quil fallait que le condamn souhaitt le bon fonctionnement de la machine. Je dis que cest le ct dfectueux. Cela est vrai, dans un sens. Mais, dans un autre sens, jtais oblig de reconnatre que tout le secret dune bonne organisation tait l. En somme, le condamn tait oblig de collaborer moralement. Ctait son intrt que tout marcht sans accroc.

Jtais oblig de constater aussi que jusquici javais eu sur ces questions des ides qui ntaient pas justes. Jai cru longtemps  et je ne sais pas pourquoi  que pour aller  la guillotine, il fallait monter sur un chafaud, gravir des marches. Je crois que ctait  cause de la Rvolution de 1789, je veux dire  cause de tout ce quon mavait appris ou fait voir sur ces questions. Mais un matin, je me suis souvenu dune photographie publie par les journaux  loccasion dune excution retentissante. En ralit, la machine tait pose  mme le sol, le plus simplement du monde. Elle tait beaucoup plus troite que je ne le pensais. Ctait assez drle que je ne men fusse pas avis plus tt. Cette machine sur le clich mavait frapp par son aspect douvrage de prcision, fini et tincelant. On se fait toujours des ides exagres de ce quon ne connat pas. Je devais constater au contraire que tout tait simple : la machine est au mme niveau que lhomme qui marche vers elle. Il la rejoint comme on marche  la rencontre dune personne. Cela aussi tait ennuyeux. La monte vers lchafaud, lascension en plein ciel, limagination pouvait sy raccrocher. Tandis que, l encore, la mcanique crasait tout : on tait tu discrtement, avec un peu de honte et beaucoup de prcision.

Il y avait aussi deux choses  quoi je rflchissais tout le temps : laube et mon pourvoi. Je me raisonnais cependant et jessayais de ny plus penser. Je mtendais, je regardais le ciel, je mefforais de my intresser. Il devenait vert, ctait le soir. Je faisais encore un effort pour dtourner le cours de mes penses. Jcoutais mon cur. Je ne pouvais imaginer que ce bruit qui maccompagnait depuis si longtemps pt jamais cesser. Je nai jamais eu de vritable imagination. Jessayais pourtant de me reprsenter une certaine seconde o le battement de ce cur ne se prolongerait plus dans ma tte. Mais en vain. Laube ou mon pourvoi taient l. Je finissais par me dire que le plus raisonnable tait de ne pas me contraindre.

Cest  laube quils venaient, je le savais. En somme, jai occup mes nuits  attendre cette aube. Je nai jamais aim tre surpris. Quand il marrive quelque chose, je prfre tre l. Cest pourquoi jai fini par ne plus dormir quun peu dans mes journes et, tout le long de mes nuits, jai attendu patiemment que la lumire naisse sur la vitre du ciel. Le plus difficile, ctait lheure douteuse o je savais quils opraient dhabitude. Pass minuit, jattendais et je guettais. Jamais mon oreille navait peru tant de bruits, distingu de sons si tnus. Je peux dire, dailleurs, que dune certaine faon jai eu de la chance pendant toute cette priode, puisque je nai jamais entendu de pas. Maman disait souvent quon nest jamais tout  fait malheureux. Je lapprouvais dans ma prison, quand le ciel se colorait et quun nouveau jour glissait dans ma cellule. Parce quaussi bien, jaurais pu entendre des pas et mon cur aurait pu clater. Mme si le moindre glissement me jetait  la porte, mme si, loreille colle au bois, jattendais perdument jusqu ce que jentende ma propre respiration, effray de la trouver rauque et si pareille au rle dun chien, au bout du compte mon cur nclatait pas et javais encore gagn vingt-quatre heures.

Pendant tout le jour, il y avait mon pourvoi. Je crois que jai tir le meilleur parti de cette ide. Je calculais mes effets et jobtenais de mes rflexions le meilleur rendement. Je prenais toujours la plus mauvaise supposition : mon pourvoi tait rejet.  Eh bien, je mourrai donc.  Plus tt que dautres, ctait vident. Mais tout le monde sait que la vie ne vaut pas la peine dtre vcue. Dans le fond, je nignorais pas que mourir  trente ans ou  soixante-dix ans importe peu puisque, naturellement, dans les deux cas, dautres hommes et dautres femmes vivront, et cela pendant des milliers dannes. Rien ntait plus clair, en somme. Ctait toujours moi qui mourrais, que ce soit maintenant ou dans vingt ans.  ce moment, ce qui me gnait un peu dans mon raisonnement, ctait ce bond terrible que je sentais en moi  la pense de vingt ans de vie  venir. Mais je navais qu ltouffer en imaginant ce que seraient mes penses dans vingt ans quand il me faudrait quand mme en venir l. Du moment quon meurt, comment et quand, cela nimporte pas, ctait vident. Donc (et le difficile ctait de ne pas perdre de vue tout ce que ce  donc  reprsentait de raisonnements), donc, je devais accepter le rejet de mon pourvoi.

 ce moment,  ce moment seulement, javais pour ainsi dire le droit, je me donnais en quelque sorte la permission daborder la deuxime hypothse : jtais graci. Lennuyeux, cest quil fallait rendre moins fougueux cet lan du sang et du corps qui me piquait les yeux dune joie insense. Il fallait que je mapplique  rduire ce cri,  le raisonner. Il fallait que je sois naturel mme dans cette hypothse, pour rendre plus plausible ma rsignation dans la premire. Quand javais russi, javais gagn une heure de calme. Cela, tout de mme, tait  considrer.

Cest  un semblable moment que jai refus une fois de plus de recevoir laumnier. Jtais tendu et je devinais lapproche du soir dt  une certaine blondeur du ciel. Je venais de rejeter mon pourvoi et je pouvais sentir les ondes de mon sang circuler rgulirement en moi. Je navais pas besoin de voir laumnier. Pour la premire fois depuis bien longtemps, jai pens  Marie. Il y avait de longs jours quelle ne mcrivait plus. Ce soir-l, jai rflchi et je me suis dit quelle stait peut-tre fatigue dtre la matresse dun condamn  mort. Lide mest venue aussi quelle tait peut-tre malade ou morte. Ctait dans lordre des choses. Comment laurais-je su puisquen dehors de nos deux corps maintenant spars, rien ne nous liait et ne nous rappelait lun  lautre.  partir de ce moment, dailleurs, le souvenir de Marie maurait t indiffrent. Morte, elle ne mintressait plus. Je trouvais cela normal comme je comprenais trs bien que les gens moublient aprs ma mort. Ils navaient plus rien  faire avec moi. Je ne pouvais mme pas dire que cela tait dur  penser.

Cest  ce moment prcis que laumnier est entr. Quand je lai vu, jai eu un petit tremblement. Il sen est aperu et ma dit de ne pas avoir peur. Je lui ai dit quil venait dhabitude  un autre moment. Il ma rpondu que ctait une visite tout amicale qui navait rien  voir avec mon pourvoi dont il ne savait rien. Il sest assis sur ma couchette et ma invit  me mettre prs de lui. Jai refus. Je lui trouvais tout de mme un air trs doux.

Il est rest un moment assis, les avant-bras sur les genoux, la tte baisse,  regarder ses mains. Elles taient fines et muscles, elles me faisaient penser  deux btes agiles. Il les a frottes lentement lune contre lautre. Puis il est rest ainsi, la tte toujours baisse, pendant si longtemps que jai eu limpression, un instant, que je lavais oubli.

Mais il a relev brusquement la tte et ma regard en face :  Pourquoi, ma-t-il dit, refusez-vous mes visites ?  Jai rpondu que je ne croyais pas en Dieu. Il a voulu savoir si jen tais bien sr et jai dit que je navais pas  me le demander : cela me paraissait une question sans importance. Il sest alors renvers en arrire et sest adoss au mur, les mains  plat sur les cuisses. Presque sans avoir lair de me parler, il a observ quon se croyait sr, quelquefois, et, en ralit, on ne ltait pas. Je ne disais rien. Il ma regard et ma interrog :  Quen pensez-vous ?  Jai rpondu que ctait possible. En tout cas, je ntais peut-tre pas sr de ce qui mintressait rellement, mais jtais tout  fait sr de ce qui ne mintressait pas. Et justement, ce dont il me parlait ne mintressait pas.

Il a dtourn les yeux et, toujours sans changer de position, ma demand si je ne parlais pas ainsi par excs de dsespoir. Je lui ai expliqu que je ntais pas dsespr. Javais seulement peur, ctait bien naturel.  Dieu vous aiderait alors, a-t-il remarqu. Tous ceux que jai connus dans votre cas se retournaient vers lui.  Jai reconnu que ctait leur droit. Cela prouvait aussi quils en avaient le temps. Quant  moi, je ne voulais pas quon maidt et justement le temps me manquait pour mintresser  ce qui ne mintressait pas.

 ce moment, ses mains ont eu un geste dagacement, mais il sest redress et a arrang les plis de sa robe. Quand il a eu fini, il sest adress  moi en mappelant  mon ami  : sil me parlait ainsi ce ntait pas parce que jtais condamn  mort ;  son avis, nous tions tous condamns  mort. Mais je lai interrompu en lui disant que ce ntait pas la mme chose et que, dailleurs, ce ne pouvait tre, en aucun cas, une consolation.  Certes, a-t-il approuv. Mais vous mourrez plus tard si vous ne mourez pas aujourdhui. La mme question se posera alors. Comment aborderez-vous cette terrible preuve ?  Jai rpondu que je laborderais exactement comme je labordais en ce moment.

Il sest lev  ce mot et ma regard droit dans les yeux. Cest un jeu que je connaissais bien. Je men amusais souvent avec Emmanuel ou Cleste et, en gnral, ils dtournaient leurs yeux. Laumnier aussi connaissait bien ce jeu, je lai tout de suite compris : son regard ne tremblait pas. Et sa voix non plus na pas trembl quand il ma dit :  Navez-vous donc aucun espoir et vivez-vous avec la pense que vous allez mourir tout entier ?  Oui , ai-je rpondu.

Alors, il a baiss la tte et sest rassis. Il ma dit quil me plaignait. Il jugeait cela impossible  supporter pour un homme. Moi, jai seulement senti quil commenait  mennuyer. Je me suis dtourn  mon tour et je suis all sous la lucarne. Je mappuyais de lpaule contre le mur. Sans bien le suivre, jai entendu quil recommenait  minterroger. Il parlait dune voix inquite et pressante. Jai compris quil tait mu et je lai mieux cout.

Il me disait sa certitude que mon pourvoi serait accept, mais je portais le poids dun pch dont il fallait me dbarrasser. Selon lui, la justice des hommes ntait rien et la justice de Dieu tout. Jai remarqu que ctait la premire qui mavait condamn. Il ma rpondu quelle navait pas, pour autant, lav mon pch. Je lui ai dit que je ne savais pas ce qutait un pch. On mavait seulement appris que jtais un coupable. Jtais coupable, je payais, on ne pouvait rien me demander de plus.  ce moment, il sest lev  nouveau et jai pens que dans cette cellule si troite, sil voulait remuer, il navait pas le choix. Il fallait sasseoir ou se lever.

Javais les yeux fixs au sol. Il a fait un pas vers moi et sest arrt, comme sil nosait avancer. Il regardait le ciel  travers les barreaux.  Vous vous trompez, mon fils, ma-t-il dit, on pourrait vous demander plus. On vous le demandera peut-tre.  Et quoi donc ?  On pourrait vous demander de voir.  Voir quoi ? 

Le prtre a regard tout autour de lui et il a rpondu dune voix que jai trouve soudain trs lasse :  Toutes ces pierres suent la douleur, je le sais. Je ne les ai jamais regardes sans angoisse. Mais, du fond du cur, je sais que les plus misrables dentre vous ont vu sortir de leur obscurit un visage divin. Cest ce visage quon vous demande de voir. 

Je me suis un peu anim. Jai dit quil y avait des mois que je regardais ces murailles. Il ny avait rien ni personne que je connusse mieux au monde. Peut-tre, il y a bien longtemps, y avais-je cherch un visage. Mais ce visage avait la couleur du soleil et la flamme du dsir : ctait celui de Marie. Je lavais cherch en vain. Maintenant, ctait fini. Et dans tous les cas, je navais rien vu surgir de cette sueur de pierre.

Laumnier ma regard avec une sorte de tristesse. Jtais maintenant compltement adoss  la muraille et le jour me coulait sur le front. Il a dit quelques mots que je nai pas entendus et ma demand trs vite si je lui permettais de membrasser :  Non , ai-je rpondu. Il sest retourn et a march vers le mur sur lequel il a pass sa main lentement :  Aimez-vous donc cette terre  ce point ?  a-t-il murmur. Je nai rien rpondu.

Il est rest assez longtemps dtourn. Sa prsence me pesait et magaait. Jallais lui dire de partir, de me laisser, quand il sest cri tout dun coup avec une sorte dclat, en se retournant vers moi :  Non, je ne peux pas vous croire. Je suis sr quil vous est arriv de souhaiter une autre vie.  Je lui ai rpondu que naturellement, mais cela navait pas plus dimportance que de souhaiter dtre riche, de nager trs vite ou davoir une bouche mieux faite. Ctait du mme ordre. Mais lui ma arrt et il voulait savoir comment je voyais cette autre vie. Alors, je lui ai cri :  Une vie o je pourrais me souvenir de celle-ci , et aussitt je lui ai dit que jen avais assez. Il voulait encore me parler de Dieu, mais je me suis avanc vers lui et jai tent de lui expliquer une dernire fois quil me restait peu de temps. Je ne voulais pas le perdre avec Dieu. Il a essay de changer de sujet en me demandant pourquoi je lappelais  monsieur  et non pas  mon pre . Cela ma nerv et je lui ai rpondu quil ntait pas mon pre : il tait avec les autres.

 Non, mon fils, a-t-il dit en mettant la main sur mon paule. Je suis avec vous. Mais vous ne pouvez pas le savoir parce que vous avez un cur aveugle. Je prierai pour vous. 

Alors, je ne sais pas pourquoi, il y a quelque chose qui a crev en moi. Je me suis mis  crier  plein gosier et je lai insult et je lui ai dit de ne pas prier. Je lavais pris par le collet de sa soutane. Je dversais sur lui tout le fond de mon cur avec des bondissements mls de joie et de colre. Il avait lair si certain, nest-ce pas ? Pourtant, aucune de ses certitudes ne valait un cheveu de femme. Il ntait mme pas sr dtre en vie puisquil vivait comme un mort. Moi, javais lair davoir les mains vides. Mais jtais sr de moi, sr de tout, plus sr que lui, sr de ma vie et de cette mort qui allait venir. Oui, je navais que cela. Mais du moins, je tenais cette vrit autant quelle me tenait. Javais eu raison, javais encore raison, javais toujours raison. Javais vcu de telle faon et jaurais pu vivre de telle autre. Javais fait ceci et je navais pas fait cela. Je navais pas fait telle chose alors que javais fait cette autre. Et aprs ? Ctait comme si javais attendu pendant tout le temps cette minute et cette petite aube o je serais justifi. Rien, rien navait dimportance et je savais bien pourquoi. Lui aussi savait pourquoi. Du fond de mon avenir, pendant toute cette vie absurde que javais mene, un souffle obscur remontait vers moi  travers des annes qui ntaient pas encore venues et ce souffle galisait sur son passage tout ce quon me proposait alors dans les annes pas plus relles que je vivais. Que mimportaient la mort des autres, lamour dune mre, que mimportaient son Dieu, les vies quon choisit, les destins quon lit, puisquun seul destin devait mlire moi-mme et avec moi des milliards de privilgis qui, comme lui, se disaient mes frres. Comprenait-il donc ? Tout le monde tait privilgi. Il ny avait que des privilgis. Les autres aussi, on les condamnerait un jour. Lui aussi, on le condamnerait. Quimportait si, accus de meurtre, il tait excut pour navoir pas pleur  lenterrement de sa mre ? Le chien de Salamano valait autant que sa femme. La petite femme automatique tait aussi coupable que la Parisienne que Masson avait pouse ou que Marie qui avait envie que je lpouse. Quimportait que Raymond ft mon copain autant que Cleste qui valait mieux que lui ? Quimportait que Marie donnt aujourdhui sa bouche  un nouveau Meursault ? Comprenait-il donc, ce condamn, et que du fond de mon avenir Jtouffais en criant tout ceci. Mais, dj, on marrachait laumnier des mains et les gardiens me menaaient. Lui, cependant, les a calms et ma regard un moment en silence. Il avait les yeux pleins de larmes. Il sest dtourn et il a disparu.

Lui parti, jai retrouv le calme. Jtais puis et je me suis jet sur ma couchette. Je crois que jai dormi parce que je me suis rveill avec des toiles sur le visage. Des bruits de campagne montaient jusqu moi. Des odeurs de nuit, de terre et de sel rafrachissaient mes tempes. La merveilleuse paix de cet t endormi entrait en moi comme une mare.  ce moment, et  la limite de la nuit, des sirnes ont hurl. Elles annonaient des dparts pour un monde qui maintenant mtait  jamais indiffrent. Pour la premire fois depuis bien longtemps, jai pens  maman. Il ma sembl que je comprenais pourquoi  la fin dune vie elle avait pris un  fianc , pourquoi elle avait jou  recommencer. L-bas, l-bas aussi, autour de cet asile o des vies steignaient, le soir tait comme une trve mlancolique. Si prs de la mort, maman devait sy sentir libre et prte  tout revivre. Personne, personne navait le droit de pleurer sur elle. Et moi aussi, je me suis senti prt  tout revivre. Comme si cette grande colre mavait purg du mal, vid despoir, devant cette nuit charge de signes et dtoiles, je mouvrais pour la premire fois  la tendre indiffrence du monde. De lprouver si pareil  moi, si fraternel enfin, jai senti que javais t heureux, et que je ltais encore. Pour que tout soit consomm, pour que je me sente moins seul, il me restait  souhaiter quil y ait beaucoup de spectateurs le jour de mon excution et quils maccueillent avec des cris de haine.`

