// Lasile de vieillards est  Marengo,  quatre-vingts kilomtres dAlger. Je prendrai lautobus  deux heures et jarriverai dans laprs-midi. Ainsi, je pourrai veiller et je rentrerai demain soir. Jai demand deux jours de cong  mon patron et il ne pouvait pas me les refuser avec une excuse pareille. Mais il navait pas lair content. Je lui ai mme dit :  Ce nest pas de ma faute.  Il na pas rpondu. Jai pens alors que je naurais pas d lui dire cela. En somme, je navais pas  mexcuser. Ctait plutt  lui de me prsenter ses condolances. Mais il le fera sans doute aprs-demain, quand il me verra en deuil. Pour le moment, cest un peu comme si maman ntait pas morte. Aprs lenterrement, au contraire, ce sera une affaire classe et tout aura revtu une allure plus officielle.

